<template>
<div v-if="showModel">
  <a-modal v-model:visible="showModel" :title="titleVal" width="400px" @cancel="cancelModel" @ok="OkModel">
    <a-checkbox-group v-model:value="checkboxData" style="width: 100%">
      <a-row>
        <a-col :span="24" v-for="(item, index) in treeData" :key="index" class="addStyle">
          <a-checkbox :value="item.id">{{ item.full_name ?  item.full_name + '-' + item.name : item.name}}</a-checkbox>
        </a-col>
      </a-row>
    </a-checkbox-group>

  </a-modal>
</div>
</template>
<script>
import { reactive, watch, toRefs  } from "vue";
import { deptListAll, setUserAuth } from "@/network/bewadmin.js";
import { message } from "ant-design-vue";
export default {
name: "UserAuth",
components: {},
props: {
    titleVal: {
      type: String,
      default: '',
    },
    visible: {
      type: Boolean,
      required: false
    },
    checkData: {
      type: String,
      default: '',
    },
    uid:  {
      type: String,
      default: '',
    }
},
setup(props, context) {
  const { visible, checkData, titleVal, uid } = toRefs(props);
  const state = reactive({
    showModel: false,
    treeData: [],
    checkboxData: []
  })
  watch(() => visible.value, (newVal) => {
    state.showModel = newVal
    let arr = checkData.value.split(',')
    arr.forEach(ele => {
      state.checkboxData.push(parseInt(ele))
    })
  },{deep: true})
  
  const cancelModel = () => {
    context.emit('closeBox')
  }
  const OkModel = () => {
    console.log(state.checkboxData)
   
    let data = {
      authData: state.checkboxData.join(','),
      uid: uid.value,
      type: titleVal.value == '设置业务权限' ? 'business' :  titleVal.value == '设置运营权限' ? 'operation' : 'finance',
    }
    setUserAuth(data).then((res) => {
      if (res) {
        message.success( '修改成功', 1);
        cancelModel()
      }
    });

  }
  
  // 获取列表数据
  const onLoadData = () => {
    deptListAll().then((res) => {
      if (res) {
        state.treeData = res
      }
    });
  };
  onLoadData()
  return {
    OkModel,
    onLoadData,
    cancelModel,
    ...toRefs(state)
  };
} 
};
</script>
<style scoped>
  .addStyle{
    margin: 8px 0;
  }
</style>