<template>
  <div>
    <a-form
      :model="formState"
      layout="inline"
      class="prjFormBox"
    >
      <a-form-item
        label="公司名称"
        name="corp_name"
      >
        <a-input v-model:value="formState.corp_name" allowClear placeholder="请输入公司名称"/>
      </a-form-item>
      <a-form-item
        label="法人"
        name="legal_name"
      >
        <a-input v-model:value="formState.legal_name" allowClear placeholder="请输入法人"/>
      </a-form-item>
      <a-form-item
        label="地区"
        name="address"
      >
        <a-input v-model:value="formState.address" allowClear placeholder="请输入地区"/>
      </a-form-item>

      <a-form-item
        label="所属行业"
        name="industry"
      >
        <a-input v-model:value="formState.industry" allowClear placeholder="请输入所属行业"/>
      </a-form-item>
      <a-form-item
        label="查询次数"
        name="start_query_num"
      >
        <a-input class="inputStyle" v-model:value="formState.start_query_num" allowClear placeholder="开始次数"/>
      </a-form-item>
      <a-form-item>
        <a-input class="inputStyle" v-model:value="formState.end_query_num" allowClear placeholder="结束次数"/>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 6, span: 12 }">
        <a-button type="primary" html-type="submit" @click="clickQuery">查询</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 6, span: 12 }">
        <a-button @click="clickInit">重置</a-button>
      </a-form-item>
    </a-form>
    <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'operation'">
          <a class="detailBtn" @click="clickLockCustomer(record)">锁定私海</a>
          <a class="detailBtn" @click="clickShiftCustomer(record)">转入客户</a>
        </template>
      </template>
    </a-table>
    <customer-one :visible="visible" :customerObj="customerObj" :titleVal="转入客户"  @closeBox="onChildClick"></customer-one>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
import { openIndex, lockCustomer} from "@/network/open.js";
import { Modal, message } from 'ant-design-vue';
import CustomerOne from '@/components/CustomerOne.vue'
export default {
  components: {
    CustomerOne
  },
  setup() {
    const state = reactive({
      customerObj: {},
      visible: false,
      formState: {
        corp_name: null,
        legal_name: null,
        industry:  null,
        start_query_num: null,
        end_query_num: null,
        address: null,
      },
      pagination: {
        total: 0,
        current: 1,
        pageSize: 10,
        onChange: (e) => {
          state.pagination.current = e
          queryList()
        }
      },
      columns: [
          {
              title: "公司名称",
              dataIndex: "corp_name",
              width: '300px',
              ellipsis: true
          },
          {
              title: "法人",
              dataIndex: "legal_name",
              width: '100px',
              ellipsis: true
          },
          {
              title: "所属行业",
              dataIndex: "industry",
              ellipsis: true
          },
          {
              title: "地区",
              dataIndex: "address",
              ellipsis: true
          },
          {
              title: "查询次数",
              dataIndex: "query_num",
              width: '100px',
              ellipsis: true
          },
          {
              title: "系统添加时间",
              dataIndex: "create_time",
              width: '200px',
              ellipsis: true
          },
          {
            title: '操作',
            dataIndex: 'operation',
            ellipsis: true
          },
      ],
      dataSource: [],
    });
    const clickQuery = () => {
      queryList()
    };
    const clickInit = () => {
      state.formState.corp_name = null
      state.formState.legal_name = null
      state.formState.industry = null
      state.formState.start_query_num = null
      state.formState.end_query_num = null
      state.formState.address = null
      state.pagination.current = 1
      queryList()
    };
    const queryList = () => {
      // 获取列表数据
      state.formState['current'] = state.pagination.current
      state.formState['pageSize'] = state.pagination.pageSize
      state.dataSource = [];
      openIndex(state.formState).then((res) => {
        if (res) {
          if (res.total === 0 || res.total) {
            state.dataSource = res.data;
            state.pagination.total = res.total;
          } else {
            state.dataSource = res;
          }
        }
      });
    };
    queryList()
    
    const clickLockCustomer = (row) => {
      Modal.confirm({
        title: '添加私海',
        content: '您确定将' + row.corp_name + '添加到私海吗？',
        cancelText: '取消',
        okText: '确定',
        onOk() {
          let data = {
            corp_open_id: row.id,
          }
          lockCustomer(data).then((res) => {
            if (res) {
              message.success('添加成功');
            }
          });
        }
      });
    }
    const clickShiftCustomer = (record) => {
      state.customerObj = record
      state.visible = true
    }
    const onChildClick = () => {
      state.customerObj = {}
      state.visible = false
    }
    
    return {
      onChildClick,
      clickShiftCustomer,
      clickLockCustomer,
      clickInit,
      queryList,
      clickQuery,
      ...toRefs(state)
    };
  }
};
</script>
<style scoped>
  .detailBtn{
    margin-right: 15px;
  }
</style>