import { request } from "./request.js";
export function addFollow(data = {}) {
    return request({
        url: "Follow/addFollow",
        method: "post",
        data
    });
}
export function customerFollowList(data = {}) {
    return request({
        url: "Follow/customerFollowList",
        method: "post",
        data
    });
}
export function followDel(data = {}) {
    return request({
        url: "Follow/followDel",
        method: "post",
        data
    });
}
