<template>
    <div>
      <a-form
        :model="formState"
        layout="inline"
        class="prjFormBox"
      >
        <a-form-item
          label="企业名称"
          name="corp_name"
        >
          <a-input allowClear v-model:value="formState.corp_name" placeholder="请输入企业名称"/>
        </a-form-item>
        <a-form-item
          label="客户姓名"
          name="name"
        >
          <a-input allowClear v-model:value="formState.name" placeholder="请输入客户姓名"/>
        </a-form-item>
  
    
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button type="primary" html-type="submit" @click="clickQuery">查询</a-button>
        </a-form-item>
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button @click="clickInit">重置</a-button>
        </a-form-item>
      </a-form>

      <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
        <template #bodyCell="{ column, record }">
          <template v-if="column.dataIndex === 'operation'">
            <a class="detailBtn" @click="clickDetail(record)">详情</a>
            <a class="deleteBtn"  @click="Del(record)">删除</a>
          </template>
        </template>
      </a-table>
      
      <customer-detail :visible="visibleDetail" :customerId="customerId" @closeBox="closeDetailComponent"></customer-detail>
    </div>
  </template>
  <script>
  import { reactive, toRefs  } from "vue";
  import { customerFollowList, followDel } from "@/network/follow.js";
  import { message, Modal } from "ant-design-vue";
  import CustomerDetail from '@/components/CustomerDetail.vue'
  export default {
    components: {
      CustomerDetail
    },
    setup() {
      const state = reactive({
        formState: {
          corp_name: null,
          name: null,
        },
        customerId: null,
        visibleDetail: false,
        pagination: {
          total: 0,
          current: 1,
          pageSize: 10,
          onChange: (e) => {
            state.pagination.current = e
            queryList()
          }
        },
        columns: [
            {
                title: "所属部门",
                dataIndex: "dept_name",
                width: '120px',
                ellipsis: true
            },
            {
                title: "所属员工",
                width: '120px',
                dataIndex: "admin_user_name",
                ellipsis: true
            },
            {
                title: "公司",
                dataIndex: "corp_name",
                width: '240px',
                ellipsis: true
            },
            {
                title: "姓名",
                dataIndex: "name",
                width: '100px',
                ellipsis: true
            },
            {
                title: "跟进方式",
                dataIndex: "follow_type_label",
                width: '150px',
                ellipsis: true
            },
            {
                title: "跟进目的",
                dataIndex: "follow_goal_label",
                width: '150px',
                ellipsis: true
            },
            {
                title: "跟进备注",
                dataIndex: "content",
                ellipsis: true
            },
            {
                title: "跟进日期",
                dataIndex: "create_time",
                width: '180px',
                ellipsis: true
            },
            {
              title: '操作',
              dataIndex: 'operation',
              width: '160px',
              ellipsis: true
            },
        ],
        dataSource: [],
      });
      const clickQuery = () => {
        queryList()
      };
      const clickInit = () => {
        state.formState.corp_name = null
        state.formState.name = null
        state.pagination.current = 1
        queryList()
      };
      const queryList = () => {
        // 获取列表数据
        state.formState['current'] = state.pagination.current
        state.formState['pageSize'] = state.pagination.pageSize
        customerFollowList(state.formState).then((res) => {
          if (res) {
            state.dataSource = res.data;
            state.pagination.total = res.total;
          }
        });
      };
      queryList()
    
      const Del = (e) => {
        Modal.confirm({
          title: "警告",
          content: "删除后不能恢复，确定删除吗？",
          okText: "确认",
          cancelText: "取消",
          onOk() {
            let data = {
              id: e.id
            }
            followDel(data).then((res) => {
              if (res) {
                message.success("删除成功", 1);
                clickInit()
              }
            });
          }
        });
      };

      const clickDetail = (e) => {
        state.customerId = e.customer_id
        state.visibleDetail = true
      }
      const closeDetailComponent = () => {
        state.visibleDetail = false
      }
      return {
        closeDetailComponent,
        clickDetail,
        Del,
        clickInit,
        queryList,
        clickQuery,
        ...toRefs(state)
      };
    }
  };
  </script>
  <style scoped>
    .detailBtn{
      margin-right: 15px;
    }
  </style>