import { createRouter,  createWebHashHistory } from "vue-router";
import Index from "../views/Index/Index.vue";
import Home from "../views/Index/Home.vue";
import Login from "../views/Login.vue";
import Userinfo from "../views/Index/Userinfo.vue";
import MenuList from "../views/Bew/MenuList.vue";
import EnumList from "../views/Bewadmin/EnumList.vue";
import DeptList from "../views/Bewadmin/DeptList.vue";
import RoleList from "../views/Bewadmin/RoleList.vue";
import UserList from "../views/Bewadmin/UserList.vue";
import OpenIndex from "../views/Open/Index.vue";
import LockCustomer from "../views/Open/LockCustomer.vue";
import MyCustomer from "../views/Business/MyCustomer.vue";
import StaffCustomer from "../views/Business/StaffCustomer.vue";
import Follow from "../views/Business/Follow.vue";
import DeptCustomer from "../views/Business/DeptCustomer.vue";
import CustomerList from "../views/Operation/CustomerList.vue";
import TestList from "../views/Bewtest/TestList.vue";

const routes = [
    {
        path: "/",
        alias: "/login",
        name: "Login",
        component: Login,
        meta: {
            title: "登录"
        }
    },
    {
        path: "/index",
        name: "Index",
        component: Index,
        meta: {
            title: "首页"
        },
        children: [
            {
                path: "home",
                name: "Home",
                component: Home,
                meta: {
                    title: "首页"
                }
            },
            {
                path: "openIndex",
                name: "OpenIndex",
                component: OpenIndex,
                meta: {
                    title: "客户公海"
                }
            },
            {
                path: "lockCustomer",
                name: "LockCustomer",
                component: LockCustomer,
                meta: {
                    title: "客户私海"
                }
            },
            {
                path: "myCustomer",
                name: "MyCustomer",
                component: MyCustomer,
                meta: {
                    title: "我的客户"
                }
            },
            {
                path: "staffCustomer",
                name: "StaffCustomer",
                component: StaffCustomer,
                meta: {
                    title: "员工客户"
                }
            },
            {
                path: "follow",
                name: "Follow",
                component: Follow,
                meta: {
                    title: "跟进记录"
                }
            },
            {
                path: "customerList",
                name: "CustomerList",
                component: CustomerList,
                meta: {
                    title: "客户列表"
                }
            },
            {
                path: "deptCustomer",
                name: "DeptCustomer",
                component: DeptCustomer,
                meta: {
                    title: "部门客户"
                }
            },
            {
                path: "userinfo",
                name: "Userinfo",
                component: Userinfo,
                meta: {
                    title: "个人中心"
                }
            },
            {
                path: "menulist",
                name: "MenuList",
                component: MenuList,
                meta: {
                    title: "导航管理"
                }
            },
            {
                path: "rolelist",
                name: "RoleList",
                component: RoleList,
                meta: {
                    title: "职务管理"
                }
            },
            {
                path: "deptList",
                name: "DeptList",
                component: DeptList,
                meta: {
                    title: "部门管理"
                }
            },
            {
                path: "enumList",
                name: "EnumList",
                component: EnumList,
                meta: {
                    title: "枚举管理"
                }
            },
            {
                path: "userlist",
                name: "UserList",
                component: UserList,
                meta: {
                    title: "用户管理"
                }
            },
            {
                path: "testlist",
                name: "TestList",
                component:  TestList,
                meta: {
                    title: "测试管理"
                }
            }
        ]
    }
];
const router = createRouter({
    history: createWebHashHistory(process.env.BASE_URL),
    routes
});

router.beforeEach((to) => {
    window.localStorage.setItem("routerPath", to.path)
    document.title = to.meta.title;
});

export default router;