<template>
    <div style="margin-bottom: 16px">
        <a-button type="primary" @click="ShowModal(1)">
            <template #icon>
                <component :is="$antIcons['PlusOutlined']" />
            </template>
            添加
        </a-button>
    </div>
    <a-modal v-model:visible="saveModal" width="600px" :title="title" wrapClassName="full-modal" okText="确定" okType="primary" cancelText="取消" @ok="OkModel" :confirm-loading="confirmLoading">
        <a-form :model="formState" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
            <a-form-item label="导航名称" :rules="[{ required: true, message: '请输入导航名称' }]">
                <a-input v-model:value="formState.label" placeholder="请输入导航名称" />
            </a-form-item>
            <a-form-item label="归属">
                <a-select ref="select" v-model:value="formState.parent_id" show-search>
                    <a-select-option :value="0">根菜单</a-select-option>
                    <a-select-option :value="item.mid" v-for="(item, index) in dataSource" :key="index">{{item.label}}</a-select-option>
                </a-select>
            </a-form-item>
            <a-form-item label="类型">
                <a-select ref="select" :value="formState.type" @change="srcChange">
                    <a-select-option :value="0">分组</a-select-option>
                    <a-select-option :value="1">模块</a-select-option>
                    <a-select-option :value="2">超链接</a-select-option>
                </a-select>
            </a-form-item>
            <a-form-item label="链接地址" v-if="is_src">
                <a-input v-model:value="formState.src" placeholder="请输入链接地址" />
            </a-form-item>
            <a-form-item label="图标">
                <a-select ref="select" v-model:value="formState.icon_class" show-search>
                    <a-select-option v-for="(item, index) in icons" :key="index" :value="item.name">{{ item.name }}</a-select-option>
                </a-select>
            </a-form-item>
            <a-form-item label="排序">
                <a-input type="number" v-model:value="formState.sort" placeholder="请输入排序值" />
            </a-form-item>
            <a-form-item label="状态">
                <a-radio-group v-model:value="formState.status">
                <a-radio :value="1">启用</a-radio>
                <a-radio :value="0">关闭</a-radio>
                </a-radio-group>
            </a-form-item>
        </a-form>
    </a-modal>
    <!-- <a-table :columns="columns" :dataSource="dataSource" bordered :pagination="false">
        <template #bodyCell="{ column, text, record }">
            <template v-if="column.dataIndex == 'icon_class'">
                <component :is="$antIcons[text]" />{{ text }}
            </template>
            
        </template>
        <template #expandedRowRender="{ record }">
            <a-table :columns="columns" :data-source="record.son" :pagination="false">
                <template #bodyCell="{ column, text, record }">
                    <template v-if="column.dataIndex == 'icon_class'">
                        <component :is="$antIcons[text]" />{{ text }}
                    </template>
                    <template v-if="column.dataIndex == 'cz'">
                        <a-button type="primary" @click="ShowModal(2, record)" style="margin-left: 10px">
                            <template #icon>
                                <component :is="$antIcons['EditOutlined']" />
                            </template>
                            修改
                        </a-button>
                        <a-button type="primary" @click="Del(record)" danger style="margin-left: 10px">
                            <template #icon>
                                <component :is="$antIcons['DeleteOutlined']" />
                            </template>
                            删除
                        </a-button>
                    </template>
                </template>
            </a-table>
        </template>
    </a-table> -->
    <a-table :columns="columns" :dataSource="dataSource" bordered :pagination="false" rowKey="label">
        <template #bodyCell="{ column, record }">
            <template v-if="column.dataIndex == 'operation'">
            <a class="detailBtn" @click="ShowModal(2, record)">编辑</a>
            <a class="detailBtn" @click="Del(record)">删除</a>
            </template>
        </template>
    </a-table>
</template>
<script>
import { reactive, toRefs } from "vue";
import { MenuLists, MenuSave, MenuDel } from "@/network/bew.js";
import { message, Modal } from "ant-design-vue";
import { useStore } from "vuex";
export default {
    setup() {
        const formState = reactive({
            mid: 0,
            parent_id: 0,
            label: "",
            type: 0,
            src: "",
            icon_class: "MenuOutlined",
            sort: 0,
            status: 0,
        });
        const store = useStore();
        const state = reactive({
            is_src: false,
            icons: store.state.icons,
            columns: [
                {
                    title: "菜单名称",
                    dataIndex: "label",
                },
                {
                    title: "类型",
                    dataIndex: "type_s",
                },
                {
                    title: "链接",
                    dataIndex: "src",
                },
                {
                    title: "图标",
                    dataIndex: "icon_class",
                },
                {
                    title: "状态",
                    dataIndex: "status_s",
                },
                {
                    title: "排序",
                    dataIndex: "sort",
                },
                {
                    title: "操作",
                    dataIndex: "operation",
                },
            ],
            dataSource: [],
            saveModal: false,
            confirmLoading: false,
            title: "添加",
            msg: "添加成功"
        });
        // 获取列表数据
        MenuLists().then((res) => {
            if (res != 1) {
                state.dataSource = res.lists;
            }
        });
        const ShowModal = (e, data) => {
            if (e == 1) {
                state.saveModal = true;
                formState.mid = 0;
                formState.parent_id = 0;
                formState.label = "";
                formState.type = 0;
                formState.src = "";
                formState.icon_class = "MenuOutlined";
                formState.sort = 0;
                formState.status = 1;
                state.title = "添加";
                state.msg = "添加成功";
                state.is_src = false;
            } else if (e == 2) {
                state.saveModal = true;
                formState.mid = data.mid;
                formState.parent_id = data.parent_id;
                formState.label = data.label;
                formState.type = data.type;
                formState.src = data.src;
                formState.icon_class = data.icon_class;
                formState.sort = data.sort;
                formState.status = data.status;
                state.title = "修改";
                state.msg = "修改成功";
                if (formState.type == 0) {
                    state.is_src = false;
                } else {
                    state.is_src = true;
                }
            }
        };
        const OkModel = () => {
            state.confirmLoading = true;
            // 先验证
            // 提交服务器
            if (!formState.label) {
                state.confirmLoading = false;
                message.error("请输入导航名称", 1);
                return false;
            }
            if (formState.parent_id != 0) {
                if (formState.type == 0) {
                state.confirmLoading = false;
                message.error("分组只能在根菜单使用", 1);
                return false;
                }
            }
            MenuSave(formState).then((res) => {
                if(res){
                    if(res != 1){
                        message.success(state.msg, 1);
                    }
                    setTimeout(() => {
                        // 重新请求列表
                        MenuLists().then((res) => {
                            if(res != 1){
                                state.dataSource = res.lists;
                                state.saveModal = false;
                                state.confirmLoading = false;
                                // 1、第一种方法，刷新页面
                                // location.reload();
                                // 2、第二种方法，刷新页面
                                // router.go(0);
                            }
                        });
                    }, 1000);
                }
            });
        };
        const Del = (data) => {
            Modal.confirm({
                title: "警告",
                content: "删除后不能恢复，确定删除吗？",
                okText: "确认",
                cancelText: "取消",
                onOk() {
                    MenuDel({ mid: data.mid }).then((res) => {
                        if(res != 1){
                            message.success("删除成功", 1);
                        }
                        MenuLists().then((res) => {
                            if (res != 1) {
                                state.dataSource = res.lists;
                            }
                        });
                    });
                },
            });
        };

        const srcChange = (e) => {
            if (e == 0) {
                state.is_src = false;
            } else {
                state.is_src = true;
            }
            formState.type = e;
        };

        return {
            ...toRefs(state),
            formState,
            ShowModal,
            OkModel,
            Del,
            srcChange
        };
    }
};
</script>
<style scoped>
  .detailBtn{
    margin-right: 15px;
  }
</style>