import { request } from "./request.js";
export function TestLists(data = {}) {
    return request({
        url: "Bewtest/testLists",
        method: "post",
        data
    });
}
export function TestSave(data = {}) {
    return request({
        url: "Bewtest/testSave",
        method: "post",
        data,
    });
}
export function TestDel(data = {}) {
    return request({
        url: "Bewtest/testDel",
        method: "post",
        data,
    });
}