export const getDeptAllList = (data)  => {
    let arr = []
    if (data && data.length > 0) {
        data.forEach(ele => {
            let obj = ele
            if (obj.full_name && obj.full_name.length > 0) {
                obj.full_name = obj.full_name + '-' + obj.name
            } else {
                obj.full_name = obj.name
            }
            arr.push(obj)
        });
    }
    return arr
}