

import { request } from "./request.js";
export function importExlTemplate(data = {}) {
    return request({
        url: "Utils/importExlTemplate",
        method: "post",
        data,
    });
}
export function uploadImg(data = {}) {
    return request({
        url: "Utils/uploadImg",
        method: "post",
        data,
    });
}