<template>
  <div v-if="showModel">
    <a-modal v-model:visible="showModel" :title="titleVal" width="800px" :footer="null" @cancel="cancelModel">
      <a-form
        :model="obj"
        :rules="rules"
        :wrapper-col="{ span: 12}"
        @finish="clickOk"
      >
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
              label="公司名称"
              name="corp_name"
              :rules="[{ required: true, message: '请输入公司名称!' }]"
            >
              <a-input :disabled="titleVal != '新增客户'" allowClear v-model:value="obj.corp_name" placeholder="请输入公司名称" maxlength="50"/>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item
              label="客户分类"
              name="classify_label"
              :rules="[{ required: true, message: '请选择客户分类!' }]"
            >
              <a-select
                v-model:value="obj.classify_label"
                allowClear
              >
                <a-select-option v-for="(item, index) in CLASSIFY" :value="item.the_value" :key="index">{{ item.the_key }}</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
              label="客户姓名"
              name="name"
              :rules="[{ required: true, message: '请输入客户姓名!' }]"
            >
              <a-input allowClear v-model:value="obj.name" placeholder="请输入客户姓名" maxlength="10"/>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item
              label="资金用途"
              name="capital_use_label"
              :rules="[{ required: true, message: '请选择资金用途!' }]"
            >
              <a-select
                v-model:value="obj.capital_use_label"
                allowClear
              >
                <a-select-option v-for="(item, index) in CAPITAL_USE" :value="item.the_value" :key="index">{{ item.the_key }}</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
              label="手机号码"
              name="mobile"
              :rules="[{ required: true, message: '请输入手机号码' },
              { pattern: /^1[23456789]\d{9}$/, message: '手机号码格式不正确' }]"
            >
              <a-input allowClear  style="width: 180px;" v-model:value="obj.mobile" :min="1" :max="11"  placeholder="请输入手机号码"/>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item
              label="手机号码"
              name="phone"
            >
              <a-input-number allowClear style="width: 180px;" v-model:value="obj.phone" :min="1" :max="11"  placeholder="请输入手机号码"/>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
              label="资金需求"
              name="money_amount"
              :rules="[{ required: true, message: '请输入资金需求!' }]"
            >
              <a-input-number allowClear style="width: 140px;" v-model:value="obj.money_amount" :min="1" :max="99999"  placeholder="请输入资金需求"/>万元
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item
              label="客戶来源"
              name="source_label"
              :rules="[{ required: true, message: '请选择客戶来源!' }]"
            >
              <a-select
                v-model:value="obj.source_label"
                allowClear
              >
                <a-select-option v-for="(item, index) in SOURCE" :value="item.the_value" :key="index">{{ item.the_key }}</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
              label="利息区间"
              name="interest_range"
              :rules="[{ required: true, message: '请输入利息区间!' }]"
            >
              <a-input allowClear v-model:value="obj.interest_range" placeholder="请输入利息区间"  maxlength="10"/>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="客户性别">
              <a-radio-group v-model:value="obj.gender">
                <a-radio :value="1">男</a-radio>
                <a-radio :value="2">女</a-radio>
              </a-radio-group>
            </a-form-item>

          </a-col>
        </a-row>
        
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item label="知晓费用">
              <a-radio-group v-model:value="obj.know_cost">
                <a-radio :value="1">是</a-radio>
                <a-radio :value="0">否</a-radio>
              </a-radio-group>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="已加微信">
              <a-radio-group v-model:value="obj.add_wechat">
                <a-radio :value="1">是</a-radio>
                <a-radio :value="0">否</a-radio>
              </a-radio-group>
            </a-form-item>
          </a-col>
        </a-row>
        
        <a-row :gutter="24">
          <a-col :span="24">
            <a-form-item
              label="备注信息"
              name="remark"
            >
              <a-textarea v-model:value="obj.remark" :rows="4" placeholder="请输入备注信息" />
            </a-form-item>
          </a-col>
        </a-row>
        
        <a-row :gutter="24">
          <a-col :span="6" :offset="18">
            <a-button  @click="cancelModel" class="cancelModel">取消</a-button>
            <a-button type="primary" html-type="submit" >确定</a-button>
          </a-col>
        </a-row>

      </a-form>
    </a-modal>
  </div>
</template>
<script>
import { reactive, watch, toRefs  } from "vue";
import { useStore } from "vuex";
import { message } from 'ant-design-vue';
import { oneCustomerAddOrEdit } from "@/network/business.js";
import { getCurrentInstance } from "@vue/runtime-core";  // 引入getCurrentInstance
export default {
  name: "ShiftCustomer", // 转入客户
  components: {},
  props: {
    titleVal: {
      type: String,
      default: '转入客户',
    },
    visible: {
      type: Boolean,
      required: false
    },
    customerObj: {
      type: Object,
      default: () => {},
      required: false
    }
  },
  setup(props, context) {
    let $bus = getCurrentInstance().appContext.config.globalProperties.$bus; // 声明$bus
    const { visible, customerObj, titleVal } = toRefs(props);
    const store = useStore();

    const state = reactive({
      showModel: false,
      obj: {
        corp_name: '',
        classify_label: null,
        name: null,
        mobile: null,
        phone: null,
        money_amount: null,
        capital_use_label: null,
        interest_range: null,
        source_label: null,
        gender: 1,
        know_cost: 1,
        add_wechat: 1,
        remark: null,
      },
      CLASSIFY: [], // 客户分类
      CAPITAL_USE: [], // 资金用途
      SOURCE: [], // 客戶来源
    })
    watch(() => visible.value, (newVal) => {
      getConfig()
      state.obj.corp_name = customerObj.value.corp_name
      if (titleVal.value == '编辑一阶') {
        state.obj.classify_label = customerObj.value.classify_label
        state.obj.name = customerObj.value.name
        state.obj.mobile = customerObj.value.mobile
        state.obj.phone = customerObj.value.phone
        state.obj.money_amount = customerObj.value.money_amount
        state.obj.capital_use_label = customerObj.value.capital_use_label
        state.obj.interest_range = customerObj.value.interest_range
        state.obj.source_label = customerObj.value.source_label
        state.obj.gender = parseInt(customerObj.value.gender)
        state.obj.know_cost = parseInt(customerObj.value.know_cost)
        state.obj.add_wechat = parseInt(customerObj.value.add_wechat)
        state.obj.remark = customerObj.value.remark
        state.obj.id = customerObj.value.id
      } else {
        state.obj.name = customerObj.value.legal_name
        state.obj.gender = 1
        state.obj.know_cost = 1
        state.obj.add_wechat = 1
      }
      state.showModel = newVal
    },{deep: true})
    const getConfig = () => {
      let configData = store.state.configs
      if (configData && configData.length > 0) {
        state.CLASSIFY = configData.filter(ele => {
          return ele.column_name == 'classify_label'
        })
        state.CAPITAL_USE = configData.filter(ele => {
          return ele.column_name == 'capital_use'
        })
        state.SOURCE = configData.filter(ele => {
          return ele.column_name == 'source'
        })
      }
    }
    const clickOk = () => {
      oneCustomerAddOrEdit(state.obj).then(res => {
        if (res) {
          if (titleVal.value == '编辑一阶') {
            message.success('编辑成功');
          } else {
            message.success('转入成功');
          }
          cancelModel()
          $bus.emit("refreshCustomerList")
        }
      })
    }
    const cancelModel = () => {
      state.obj = {}
      context.emit('closeBox')
    }
    return {
      clickOk,
      cancelModel,
      ...toRefs(state)
    };
  } 
};
</script>
<style scoped>
  .cancelModel{
    margin-right: 20px;
  }
</style>