<template>
  <div v-if="showModel">
    <a-modal v-model:visible="showModel" :title="titleVal" width="1000px" :footer="null" @cancel="cancelModel">
      <a-form
        :model="obj"
        :rules="rules"
        :wrapper-col="{ span: 12}"
