      >
        <a-row :gutter="24">
          <a-col :span="8">
            <a-form-item
              label="客户姓名"
              name="name"
            >
              <a-input v-model:value="obj.name" disabled/>
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="身份证"
              name="id_card"
              :rules="[{ required: true, message: '请输入身份证号码', trigger: 'blur' },{
                pattern: /^[1-9]\d{5}(18|19|20|(3\d))\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/,
                message: '身份证号码格式不正确'
              }]"
            >
              <a-input v-model:value="obj.id_card" placeholder="请输入身份证号码" maxlength="18"/>
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="籍贯"
              name="native_province"
              :rules="[{ required: true, trigger: 'change', message: '请输入籍贯' }]"
            >
              <a-cascader 
                ref="cascader"
                :field-names="{ label: 'name', value: 'code' }" 
                v-model:value="areaValue" 
                :options="areaList" 
                placeholder="所在地区" 
                @change="changeArea"
              />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="8">
            <a-form-item
              label="个人银行卡"
              name="oneself_bank"
              :rules="[{ required: true, message: '请输入个人银行', trigger: 'blur' }]"
            >
              <a-input v-model:value="obj.oneself_bank" placeholder="请输入个人银行卡" maxlength="20" />
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="银行卡号"
              name="oneself_bank_num"
              :rules="[{ required: true, message: '请输入个人银行卡号', trigger: 'blur' },
              { required: false, message: '银行卡号只能为数字组成', type: 'number', trigger: 'blur',
                transform(value) {
                  return Number(value)
                }
              }]"
            >
              <a-input v-model:value="obj.oneself_bank_num" placeholder="请输入个人银行卡号" maxlength="20"  />
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="现居地址"
              name="house_address"
              :rules="[{ required: true, message: '请输入现居住地址', trigger: 'blur' }]"
            >
              <a-input v-model:value="obj.house_address" placeholder="请输入现居地址" maxlength="120"  />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="8">
            <a-form-item
              label="对公银行"
              name="corp_bank"
              :rules="[{ required: true, message: '请输入企业对公银行', trigger: 'blur' }]"
            >
              <a-input v-model:value="obj.corp_bank" placeholder="请输入企业对公银行" maxlength="20" />
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="资金需求"
              name="money_amount"
              :rules="[{ required: true, message: '请输入资金需求', trigger: 'blur' },
              { min: 1, type: 'number', message: '金额需大于0', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 120px;" v-model:value="obj.money_amount" :min="0" :max="99999"  placeholder="资金需求"/>万元
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
