        </a-row>
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
              label="婚姻状况"
              name="spouse_status_label"
              :rules="[{ required: true, message: '请选择婚姻状况', trigger: 'change' }]"
            >
              <a-radio-group v-model:value="obj.spouse_status_label">
                <template v-for="(item, index) in SPOUSE_STATUS" :key="index">
