              :rules="[{ required: true, message: '请输入配偶姓名', trigger: 'blur' }]"
            >
              <a-input v-model:value="obj.spouse_name" placeholder="请输入配偶姓名" maxlength="20" />
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="身份证"
              name="spouse_id_card"
              :rules="[{ required: true, message: '请输入配偶身份证号码', trigger: 'blur' },
              {
                pattern: /^[1-9]\d{5}(18|19|20|(3\d))\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/,
                message: '身份证号码格式不正确'
              }]"
            >
              <a-input v-model:value="obj.spouse_id_card" placeholder="请输入身份证号码" maxlength="18" />
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="配偶电话"
              name="spouse_mobile"
              :rules="[{ required: true, message: '请输入配偶手机号码', trigger: 'blur' },
              { pattern: /^1[23456789]\d{9}$/, message: '手机号码格式不正确' }]"
            >
              <a-input v-model:value="obj.spouse_mobile" placeholder="请输入电话电话" maxlength="11" />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="8">
            <a-form-item
              label="紧急联系人"
              name="link_man_one_name"
              :rules="[{ required: true, message: '请输入紧急联系人姓名', trigger: 'blur' }]"
            >
              <a-input v-model:value="obj.link_man_one_name" placeholder="请输入紧急联系人" maxlength="20" />
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="关系"
              name="link_man_one_relation"
              :rules="[{ required: true, message: '请输入紧急联系人关系', trigger: 'blur' }]"
            >
