            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="联系电话"
              name="link_man_one_mobile"
              :rules="[{ required: true, message: '请输入紧急联系电话', trigger: 'blur' },
              { pattern: /^1[23456789]\d{9}$/, message: '手机号码格式不正确' }]"
            >
              <a-input v-model:value="obj.link_man_one_mobile" placeholder="请输入联系电话" maxlength="11" />
            </a-form-item>
          </a-col>
        </a-row>

        <a-row :gutter="24">
          <a-col :span="8">
            <a-form-item
              label="紧急联系人"
              name="link_man_two_name"
            >
              <a-input v-model:value="obj.link_man_two_name" placeholder="请输入紧急联系人" maxlength="20" />
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="关系"
              name="link_man_two_relation"
            >
              <a-input v-model:value="obj.link_man_two_relation" placeholder="请输入关系" maxlength="20" />
            </a-form-item>
          </a-col>
          <a-col :span="8">
            <a-form-item
              label="联系电话"
              name="link_man_two_mobile"
            >
              <a-input v-model:value="obj.link_man_two_mobile" placeholder="请输入联系电话" maxlength="11" />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="24">
            <a-form-item
              label="房产"
              name="assets_house_type_label"
              :rules="[{ required: true, message: '请选择房产信息', trigger: 'change' }]"
            >
              <a-radio-group v-model:value="obj.assets_house_type_label">
                <template v-for="(item, index) in HOUSE_TYPE" :key="index">
                  <a-radio :value="item.the_value">{{ item.the_key }}</a-radio>
                </template>
              </a-radio-group>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24" v-if="obj.assets_house_type_label !=='无'">
          <a-col :span="6">
            <a-form-item
              label="房产地址"
              name="assets_house_address"
              :rules="[{ required: true, message: '请输入房产地址', trigger: 'blur' }]"
            >
              <a-input v-model:value="obj.assets_house_address" placeholder="请输入房产地址" maxlength="50" />
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="房产性质"
              name="assets_house_nature_label"
              :rules="[{ required: true, message: '请选择房产性质', trigger: 'change' }]"
            >
              <a-select
                v-model:value="obj.assets_house_nature_label"
                allowClear
              >
                <a-select-option v-for="(item, index) in HOUSE_NATURE" :value="item.the_value" :key="index">{{ item.the_key }}</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="房产价值"
              name="assets_house_value"
              :rules="[{ required: true, message: '请输入房产价值', trigger: 'blur' },
              { min: 1, max: 100000, type: 'number', message: '金额需大于0小于99999', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 100px;" v-model:value="obj.assets_house_value" :min="0" :max="99999"  placeholder="房产价值"/>万元
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="房产证件"
              name="assets_house_paper"
              :rules="[{ required: true, message: '请选择有无房产证', trigger: 'change' }]"
            >
              <a-radio-group v-model:value="obj.assets_house_paper">
                <a-radio :value="1">有</a-radio>
                <a-radio :value="0">无</a-radio>
              </a-radio-group>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="24">
            <a-form-item
              label="车辆"
              name="assets_car_type_label"
              :rules="[{ required: true, message: '请选择车辆信息', trigger: 'change' }]"
            >
              <a-radio-group v-model:value="obj.assets_car_type_label">
                <template v-for="(item, index) in CAR_TYPE" :key="index">
                  <a-radio :value="item.the_value">{{ item.the_key }}</a-radio>
                </template>
              </a-radio-group>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24" v-if="obj.assets_car_type_label !=='无'">
          <a-col :span="6">
            <a-form-item
              label="车辆品牌"
              name="assets_car_brand"
              :rules="[{ required: true, message: '请输入车辆品牌', trigger: 'blur' }]"
            >
              <a-input v-model:value="obj.assets_car_brand" placeholder="请输入车辆品牌" maxlength="20" />
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="裸车价格"
              name="assets_car_naked_price"
              :rules="[{ required: true, message: '请输入裸车价', trigger: 'blur' },
              { min: 1, max: 10000, type: 'number', message: '金额需大于0小于9999', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 100px;" v-model:value="obj.assets_car_naked_price" :min="0" :max="99999"  placeholder="裸车价格"/>万元
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="车辆年限"
              name="assets_car_age"
              :rules="[{ required: true, message: '请出入车辆年限', trigger: 'blur' },
              { min: 1, type: 'number', message: '年限需大于0', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 120px;" v-model:value="obj.assets_car_age" :min="0" :max="99999"  placeholder="车辆年限"/>年
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="车辆里程"
              name="assets_car_km"
              :rules="[{ required: true, message: '请出入车辆里程', trigger: 'blur' },
              { min: 1, type: 'number', message: '车辆里程需大于0', trigger: 'blur' }]"
            >
            <a-input-number allowClear style="width: 100px;" v-model:value="obj.assets_car_km" :min="0" :max="99999"  placeholder="车辆里程"/>公里
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="6">
            <a-form-item
              label="身份证正面"
              name="id_card_front_img"
            >
              <update-img :imgUrl="obj.id_card_front_img" source="id_card_front_img"></update-img>
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="身份证反面"
              name="id_card_back_img"
            >
              <update-img :imgUrl="obj.id_card_back_img" source="id_card_back_img"></update-img>
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="营业执照"
              name="corp_credit_img"
            >
              <update-img :imgUrl="obj.corp_credit_img" source="corp_credit_img"></update-img>
            </a-form-item>
          </a-col>
          <a-col :span="6">
            <a-form-item
              label="开户许可证"
              name="corp_licence_img"
            >
              <update-img :imgUrl="obj.corp_licence_img" source="corp_licence_img"></update-img>
            </a-form-item>
          </a-col>
        </a-row>

        <a-row :gutter="24">
          <a-col :span="6" :offset="18">
            <a-button  @click="cancelModel" class="cancelModel">取消</a-button>
            <a-button type="primary" html-type="submit" >确定</a-button>
          </a-col>
        </a-row>

      </a-form>
    </a-modal>
  </div>
</template>
<script>
import { reactive, watch, toRefs,defineComponent   } from "vue";
import { message } from 'ant-design-vue';
import { threeCustomerAddOrEdit } from "@/network/business.js";
import { getCurrentInstance } from "@vue/runtime-core";  // 引入getCurrentInstance
import { area } from '@/utils/area.js';
import { useStore } from "vuex";
import UpdateImg from '@/components/UpdateImg.vue'
export default defineComponent({
  name: "ShiftCustomer", // 转入客户
  components: {
    UpdateImg
  },
  props: {
    subType: {
      type: String,
      default: '1', // 提交类型  1：添加  2：编辑
    },
    titleVal: {
      type: String,
      default: '进三阶',
    },
    visible: {
      type: Boolean,
      required: false
    },
    customerObj: {
      type: Object,
      default: () => {},
      required: false
    }
  },
  setup(props, context) {
    let $bus = getCurrentInstance().appContext.config.globalProperties.$bus; // 声明$bus
    const { visible, customerObj, titleVal, subType } = toRefs(props);
    const store = useStore();
    $bus.on("setUpdateImg", (e) => {
      state.obj[e.type] = e.url
    });
    

    const state = reactive({
      areaValue: null,
      areaList: [],
      showModel: false,
      obj: {
        id: null,
        make_req_label: '加急',
        assets_house_type_label: '无',
        assets_car_type_label: '无',
        spouse_status_label: '已婚',
        have_couple_sign: 1,
        name: null,
        id_card: null,
        native_province: null,
        native_city: null,
        oneself_bank: null,
        oneself_bank_num: null,
        house_address: null,
        corp_bank: null,
        money_amount: null,
        repay_type_label: null,
        state_tax_pwd: null,
        spouse_name: null,
        spouse_id_card: null,
        spouse_mobile: null,
        link_man_one_name: null,
        link_man_one_relation: null,
        link_man_one_mobile: null,
        link_man_two_name: null,
        link_man_two_relation: null,
        link_man_two_mobile: null,
        assets_house_address: null,
        assets_house_nature_label: null,
        assets_house_value: null,
        assets_house_paper: null,
        assets_car_brand: null,
        assets_car_naked_price: null,
        assets_car_age: null,
        assets_car_km: null,
        id_card_front_img: null,
        id_card_back_img: null,
        corp_credit_img: null,
        corp_licence_img: null,
      },
      REPAY_TYPE: [],
      MAKE_REQ: [],
      SPOUSE_STATUS: [],
      HOUSE_TYPE: [],
      HOUSE_NATURE: [],
      CAR_TYPE: []
    })
    watch(() => visible.value, (newVal) => {
      let arr = []
      area.forEach((ele, index) => {
        arr.push({
          name: ele.name,
          code: ele.code
        })
        if (ele.children && ele.children.length > 0) {
          arr[index]['children'] = []
          ele.children.forEach(el => {
            arr[index]['children'].push({
              name: el.name,
              code: el.code
            })
          })
        }
      })
      state.areaList = arr
      getConfig()
      state.obj.id = customerObj.value.id
      state.obj.name = customerObj.value.name
      if (titleVal.value == '编辑三阶') {
        state.areaValue = [customerObj.value.native_province, customerObj.value.native_city ]
        state.obj.make_req_label = customerObj.value.make_req_label
        state.obj.assets_house_type_label = customerObj.value.assets_house_type_label
        state.obj.assets_car_type_label = customerObj.value.assets_car_type_label
        state.obj.spouse_status_label = customerObj.value.spouse_status_label
        state.obj.have_couple_sign = customerObj.value.have_couple_sign
        state.obj.id_card = customerObj.value.id_card
        state.obj.native_province = customerObj.value.native_province
        state.obj.native_city = customerObj.value.native_city
        state.obj.oneself_bank = customerObj.value.oneself_bank
        state.obj.oneself_bank_num = customerObj.value.oneself_bank_num
        state.obj.house_address = customerObj.value.house_address
        state.obj.corp_bank = customerObj.value.corp_bank
        state.obj.money_amount = customerObj.value.money_amount
        state.obj.repay_type_label = customerObj.value.repay_type_label
        state.obj.state_tax_pwd = customerObj.value.state_tax_pwd
        state.obj.spouse_name = customerObj.value.spouse_name
        state.obj.spouse_id_card = customerObj.value.spouse_id_card
        state.obj.spouse_mobile = customerObj.value.spouse_mobile
        state.obj.link_man_one_name = customerObj.value.link_man_one_name
        state.obj.link_man_one_relation = customerObj.value.link_man_one_relation
        state.obj.link_man_one_mobile = customerObj.value.link_man_one_mobile
        state.obj.link_man_two_name = customerObj.value.link_man_two_name
        state.obj.link_man_two_relation = customerObj.value.link_man_two_relation
        state.obj.link_man_two_mobile = customerObj.value.link_man_two_mobile
        state.obj.assets_house_address = customerObj.value.assets_house_address
        state.obj.assets_house_nature_label = customerObj.value.assets_house_nature_label
        state.obj.assets_house_value = customerObj.value.assets_house_value
        state.obj.assets_house_paper = parseInt(customerObj.value.assets_house_paper)
        state.obj.assets_car_brand = customerObj.value.assets_car_brand
        state.obj.assets_car_naked_price = customerObj.value.assets_car_naked_price
        state.obj.assets_car_age = customerObj.value.assets_car_age
        state.obj.id_card_front_img = customerObj.value.id_card_front_img
        state.obj.assets_car_km = customerObj.value.assets_car_km
        state.obj.id_card_back_img = customerObj.value.id_card_back_img
        state.obj.corp_credit_img = customerObj.value.corp_credit_img
        state.obj.corp_licence_img = customerObj.value.corp_licence_img
      } 
      state.showModel = newVal
    },{deep: true})
    const getConfig = () => {
      let configData = store.state.configs
      if (configData && configData.length > 0) {
        state.REPAY_TYPE = configData.filter(ele => {
          return ele.column_name == 'repayment_type'
        })
        state.MAKE_REQ = configData.filter(ele => {
          return ele.column_name == 'make_req'
        })
        state.SPOUSE_STATUS = configData.filter(ele => {
          return ele.column_name == 'spouse_status'
        })
        state.HOUSE_TYPE = configData.filter(ele => {
          return ele.column_name == 'house_type'
        })
        state.HOUSE_NATURE = configData.filter(ele => {
          return ele.column_name == 'house_nature'
        })
        state.CAR_TYPE = configData.filter(ele => {
          return ele.column_name == 'car_type'
        })
      }
    }
    const clickOk = () => {
      let data = {
        obj: state.obj,
        type: subType.value
      }
      threeCustomerAddOrEdit(data).then(res => {
        if (res) {
          if (titleVal.value == '编辑三阶') {
            message.success('编辑成功');
          } else {
            message.success('进三阶成功');
          }
          cancelModel()
          $bus.emit("refreshCustomerList")
        }
      })
    }
    const cancelModel = () => {
      state.areaValue = null
      state.obj = {
        id: null,
        make_req_label: '加急',
        assets_house_type_label: '无',
        assets_car_type_label: '无',
        spouse_status_label: '已婚',
        have_couple_sign: 1,
        name: null,
        id_card: null,
        native_province: null,
        native_city: null,
        oneself_bank: null,
        oneself_bank_num: null,
        house_address: null,
        corp_bank: null,
        money_amount: null,
        repay_type_label: null,
        state_tax_pwd: null,
        spouse_name: null,
        spouse_id_card: null,
        spouse_mobile: null,
        link_man_one_name: null,
        link_man_one_relation: null,
        link_man_one_mobile: null,
        link_man_two_name: null,
        link_man_two_relation: null,
        link_man_two_mobile: null,
        assets_house_address: null,
        assets_house_nature_label: null,
        assets_house_value: null,
        assets_house_paper: null,
        assets_car_brand: null,
        assets_car_naked_price: null,
        assets_car_age: null,
        assets_car_km: null,
        id_card_front_img: null,
        id_card_back_img: null,
        corp_credit_img: null,
        corp_licence_img: null,
      }
      context.emit('closeBox')
    }

    const changeArea = (e) => {
      if (e  && e.length > 0) {
        state.obj.native_province = e[0]
        state.obj.native_city = e[1]
      } else {
        state.obj.native_province = null
        state.obj.native_city = null
      }
    }
    return {
      changeArea,
      clickOk,
      cancelModel,
      ...toRefs(state)
    };
  } 
});
</script>
<style scoped>
  .cancelModel{
    margin-right: 20px;
  }
  /deep/ .ant-col-12{
    max-width: 100%;
  }
</style>