<template>
  <div v-if="showModel">
    <a-modal v-model:visible="showModel" title="客户跟进" width="600px" :footer="null" @cancel="cancelModel">
      <a-form
        :model="obj"
        :rules="rules"
        @finish="clickOk"
        :label-col="labelCol"
      >
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
            label="公司名称"
            name="corp_name"
            >
            <a-input :disabled="true" allowClear v-model:value="obj.corp_name" maxlength="50"/>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
            label="跟进方式"
            name="follow_type_label"
            >
            <a-select
              v-model:value="obj.follow_type_label"
              allowClear
              placeholder="跟进方式"
            >
              <a-select-option v-for="(item, index) in followTypeLabel" :key="index" :value="item.the_value">{{ item.the_key }}</a-select-option>
            </a-select>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :span="12">
            <a-form-item
            label="跟进目的"
            name="follow_goal_label"
            >
            <a-select
              v-model:value="obj.follow_goal_label"
              allowClear
              placeholder="跟进目的"
            >
              <a-select-option v-for="(item, index) in followGoalLabel" :key="index" :value="item.the_value">{{ item.the_key }}</a-select-option>
            </a-select>
            </a-form-item>
          </a-col>
        </a-row>
          <a-row :gutter="24">
            <a-col :span="24">
              <a-form-item
              label="跟进内容"
              name="content"
              :rules="[{ required: true, message: '请输入跟进内容!' }]"
              >
              <a-textarea v-model:value="obj.content" :rows="4" placeholder="请输入跟进内容" />
              </a-form-item>
            </a-col>
          </a-row>
              
          <a-row :gutter="24">
            <a-col :span="8" :offset="17">
              <a-button  @click="cancelModel" class="cancelModel">取消</a-button>
              <a-button type="primary" html-type="submit" >确定</a-button>
            </a-col>
          </a-row>

      </a-form>
    </a-modal>
  </div>
</template>
<script>
import { reactive, watch, toRefs  } from "vue";
import { message } from 'ant-design-vue';
import { addFollow } from "@/network/follow.js";
import { getCurrentInstance } from "@vue/runtime-core";  // 引入getCurrentInstance
import { useStore } from "vuex";
export default {
  name: "ShiftCustomer", // 转入客户
  components: {},
  props: {
    customerObj: {
      type: Object,
      default: () => {},
      required: false
    },
    visible: {
      type: Boolean,
      required: false
    }
  },
  setup(props, context) {
    let $bus = getCurrentInstance().appContext.config.globalProperties.$bus; // 声明$bus
    const { visible, customerObj } = toRefs(props);
    const store = useStore();

    const state = reactive({
      showModel: false,
      obj: {
        follow_goal_label: null,
        follow_type_label: null,
        corp_name: null,
        customer_id: null,
        admin_user_id: null,
        content: null
      },
      followTypeLabel: store.state.followTypeLabel,
      followGoalLabel: store.state.followGoalLabel,
    })
    watch(() => visible.value, (newVal) => {
      getConfig()
      state.showModel = newVal
      state.obj.corp_name = customerObj.value.corp_name
      state.obj.customer_id = customerObj.value.id
      state.obj.admin_user_id = customerObj.value.admin_user_id
    },{deep: true})
  
  
    const cancelModel = () => {
      state.obj = {}
      context.emit('closeBox')
    }
    const clickOk = () => {
      addFollow(state.obj).then(res => {
        if (res) {
          message.success('跟进成功');
          cancelModel()
          $bus.emit("refreshCustomerList")
        }
      })
    }
    const getConfig = () => {
      let configData = store.state.configs
      if (configData && configData.length > 0) {
        state.followTypeLabel = configData.filter(ele => {
          return ele.column_name == 'follow_type_label'
        })
        state.followGoalLabel = configData.filter(ele => {
          return ele.column_name == 'follow_goal_label'
        })
      }
    }
    return {
      clickOk,
      labelCol: { style: { width: '80px' } },
      cancelModel,
      ...toRefs(state)
    };
  } 
};
</script>
<style scoped>
  .cancelModel{
    margin-right: 20px;
  }
</style>