import { request } from "./request.js";
/**
 * 枚举列表
 * @param table_name 表名
 * @param column_name 列名
 * @param the_key 名称
 * @param current 分页起始位置
 * @param pageSize 分页大小
*/
export function EnumList(data) {
    return request({
        url: "Enum/enumList",
        method: "post",
        data,
    });
}
/**
 * 枚举添加、修改
 * @param id 枚举id 编辑传入
 * @param table_name 表名
 * @param column_name 列名
 * @param the_key 枚举名称
 * @param the_value 枚举值
 * @param sort 枚举排序
 * @param remark 枚举备注
*/
export function EnumAddOrEdit(data) {
    return request({
        url: "Enum/enumAddOrEdit",
        method: "post",
        data,
    });
}
/**
 * 枚举删除
 * @param id 枚举id
*/
export function EnumDel(data) {
    return request({
        url: "Enum/enumDel",
        method: "post",
        data,
    });
}