<template>
    <a-layout style="min-height: 100vh" id="oyk">
        <a-layout-sider v-model:collapsed="collapsed" collapsible>
        <div class="logo">
            <router-link to="/index/home" class="router">
            <img src="@/assets/logo.jpg" alt="logo" />
            <h1>青鸟系统</h1>
            </router-link>
        </div>
        <a-menu v-model:selectedKeys="selectedKeys" theme="dark" mode="inline">
            <a-menu-item key="-1">
                <router-link to="/index/home">
                    <component :is="$antIcons['DesktopOutlined']" />
                    <span>首页</span>
                </router-link>
            </a-menu-item>
            <template v-for="item in menu">
                <a-menu-item v-bind:key="item.mid" v-if="item.type == 2">
                    <a :href="item.src" target="_blank">
                        <component :is="$antIcons[item.icon_class]" />
                        <span>{{ item.label }}</span>
                    </a>
                </a-menu-item>
                <a-menu-item v-bind:key="item.mid" v-else-if="item.type == 1">
                    <router-link :to="item.src">
                        <component :is="$antIcons[item.icon_class]" />
                        <span>{{ item.label }}</span>
                    </router-link>
                </a-menu-item>
                <a-sub-menu v-else-if="item.type == 0" v-bind:key="item.mid">
                    <template #title>
                        <span>
                            <component :is="$antIcons[item.icon_class]" />
                            <span>{{ item.label }}</span>
                        </span>
                    </template>
                    <a-menu-item v-for="son in item.children" v-bind:key="son.mid">
                        <a :href="son.src" target="_blank" v-if="son.type == 2">
                            <component :is="$antIcons[son.icon_class]" />
                            <span>{{ son.label }}</span>
                        </a>
                        <router-link :to="son.src" v-show="son.src" v-else>
                            <component :is="$antIcons[son.icon_class]" />
                            <span>{{ son.label }}</span>
                        </router-link>
                    </a-menu-item>
                </a-sub-menu>
            </template>
            <a-menu-item key="-2">
                <router-link to="/index/userinfo">
                    <component :is="$antIcons['UserOutlined']" />
                    <span>个人中心</span>
                </router-link>
            </a-menu-item>
        </a-menu>
        </a-layout-sider>
        <a-layout>
        <a-layout-header style="background: #fff; padding: 0 20px 0 0">
            <div style="float: right">
                <a-dropdown placement="bottomRight">
                    <span
                        class="ant-dropdown-link"
                        style="rgba(0, 0, 0, 0.85);cursor: pointer;"
                        @click.prevent
                        >
                        {{ user.name }}
                        <component :is="$antIcons['DownOutlined']" />
                    </span>
                    <template #overlay>
                        <a-menu @click="handleMenuClick">
                            <router-link to="/userinfo"
                            ><a-menu-item key="-2">个人中心</a-menu-item></router-link
                            >
                            <a-menu-divider />
                            <a-menu-item key="100" @click="tologout">退出</a-menu-item>
                        </a-menu>
                    </template>
                </a-dropdown>
            </div>
        </a-layout-header>
        <a-layout-content style="margin: 16px 16px">
            <div :style="{ padding: '24px', background: '#fff', minHeight: '360px' }">
                <router-view />
            </div>
        </a-layout-content>
        </a-layout>
    </a-layout>
</template>
<script>
import { reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import { Index } from "@/network/index.js";
import { EnumList } from "@/network/enum.js";
import { message } from "ant-design-vue";
import { useStore } from "vuex";
export default {
    name: "Index",
    setup() {
        const store = useStore();
        // 用户登录
        const router = useRouter();
        const token = window.localStorage.getItem("token");
        if (token) {
            const routerPath = window.localStorage.getItem("routerPath");
            if (routerPath) {
                router.push(routerPath);
            } else {
                router.push("/index/home");
            }
        } else {
            message.error("请先登录", 1);
            router.push("/login");
        }

        const user = JSON.parse(window.localStorage.getItem("user"));

        // 获取左侧菜单和用户信息
        const state = reactive({
            user: user,
            menu: [],
            collapsed: false,
            selectedKeys: [1]
        });
        Index().then((res) => {
            if (res) {
                state.menu = res.menu;
            }
        });
        // 退出登录
        const tologout = () => {
            window.localStorage.setItem("token", "");
            window.localStorage.setItem("routerPath", "");
            message.success("退出成功", 1);
            setTimeout(() => {
                router.push({ path: "/login" });
            }, 500);
        };

        // 初始化枚举
        let data = {
            table_name: 'select',
            column_name: '',
            the_key: '',
            current: 1,
            pageSize: 20000,
        }
        EnumList(data).then(res => {
            store.commit('setConfigs', res.data)
        })
        return {
            ...toRefs(state),
            tologout
        };
    }
};
</script>
<style>
#oyk .logo {
    position: relative;
    display: flex;
    align-items: center;
    padding: 16px 16px;
    line-height: 32px;
    cursor: pointer;
}
#oyk .logo .router {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 32px;
}
#oyk .logo img {
    display: inline-block;
    height: 32px;
    vertical-align: middle;
    transition: height 0.2s;
    margin-left: 10px;
}
#oyk .logo h1 {
    margin: 0 0 0 24px;
    overflow: hidden;
    color: #fff;
    font-weight: 600;
    font-size: 18px;
    line-height: 32px;
    -webkit-animation: fade-in;
    animation: fade-in;
    -webkit-animation-duration: 0.2s;
    animation-duration: 0.2s;
    display: inline-block;
    height: 32px;
    vertical-align: middle;
}
</style>