<template>
    <a-form
      :model="formSelect"
      layout="inline"
      class="prjFormBox"
    >
      <a-form-item
        label="账号"
        name="account"
      >
        <a-input v-model:value="formSelect.account" placeholder="请输入账号"/>
      </a-form-item>
      <a-form-item
        label="用户名"
        name="name"
      >
        <a-input v-model:value="formSelect.name" placeholder="请输入用户名"/>
      </a-form-item>
      <a-form-item
        label="所属部门"
        name="dept_id"
        calss="deptName"
      >
        <a-select
          v-model="formSelect.dept_id"
          show-search
          placeholder="搜索所属部门"
          :options="deptListAllData"
          :field-names="{ label: 'full_name', value: 'id'}"
          :filter-option="filterOption"
          @change="changeDept"
        ></a-select>
      </a-form-item>
      
      <a-form-item label="所属部门">
        
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button type="primary" html-type="submit" @click="queryList">查询</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button @click="clickInit">重置</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button type="primary" html-type="submit" @click="ShowModal(1)">新增</a-button>
      </a-form-item>
    </a-form>
    <a-modal v-model:visible="saveModal" width="600px" :title="title" wrapClassName="full-modal" okText="确定" okType="primary" cancelText="取消" @ok="OkModel">
        <a-form :model="formState" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
            <a-form-item label="账号" :rules="[{ required: true, message: '请输入手机号' }]">
                <a-input v-model:value="formState.account" placeholder="请输入手机号"/>
            </a-form-item>
            <a-form-item label="姓名">
                <a-input v-model:value="formState.name" placeholder="请输入真实姓名" />
            </a-form-item>
            <a-form-item label="职务">
                <a-select ref="select" v-model:value="formState.role_id">
                    <a-select-option value="0">请选择</a-select-option>
                    <template v-for="(item, index) in role" :key="index">
                      <a-select-option :value="item.role_id">{{item.role_name}}</a-select-option>
                    </template>
                </a-select>
            </a-form-item>
            <a-form-item label="所属部门">
              <a-select
                v-model="formState.dept_id"
                show-search
                placeholder="搜索所属部门"
                :options="deptListAllData"
                :field-names="{ label: 'full_name', value: 'id'}"
                :filter-option="filterOption"
                @change="handleChange"
              ></a-select>
            </a-form-item>
            <a-form-item label="微信号">
                <a-input v-model:value="formState.we_chat" placeholder="请输入微信号" />
            </a-form-item>
            <a-form-item label="性别">
                <a-radio-group v-model:value="formState.sex">
                    <a-radio :value="1">男</a-radio>
                    <a-radio :value="2">女</a-radio>
                </a-radio-group>
            </a-form-item>
            <a-form-item label="状态">
                <a-radio-group v-model:value="formState.status">
                    <a-radio :value="1">启用</a-radio>
                    <a-radio :value="0">关闭</a-radio>
                </a-radio-group>
            </a-form-item>
        </a-form>
    </a-modal>
    <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'authData'">
          <a class="detailBtn" @click="setBusinessAuth(record)">业务</a>
          <!-- <a class="detailBtn" >运营</a> -->
          <!-- <a class="detailBtn" >财务</a> -->
        </template>
        <template v-if="column.dataIndex === 'operation'">
          <a class="detailBtn"  @click="ShowModal(2, record)">编辑</a>
          <a class="detailBtn" @click="Del(record)">删除</a>
        </template>
      </template>
    </a-table>

    <user-auth :visible="visible" :titleVal="titleVal" :uid="uid" :checkData="checkData" @closeBox="closeComponent"></user-auth>
</template>
<script>
import { reactive, toRefs } from "vue";
import { UserLists, UserSave, UserDel, deptListAll} from "@/network/bewadmin.js";
import { message, Modal } from "ant-design-vue";
import { getDeptAllList } from '@/utils/utils.js';
import UserAuth from '@/components/UserAuth.vue'
export default {
  components: {
    UserAuth
  },
  setup() {
    const formSelect = reactive({
      name: "",
      account: "",
      dept_id: null,
    });
    const formState = reactive({
      uid: 0,
      dept_id: null,
      name: "",
      account: "",
      password: "",
      we_chat: "",
      sex: 1,
      role_id: "0",
      status: 1
    });
    const state = reactive({
      visible: false,
      titleVal: '',
      deptListAllData: [],
      pagination: {
        total: 0,
        current: 1,
        pageSize: 10,
        onChange: (e) => {
          formSelect.current = e
          state.pagination.current = e
          queryList()
        }
      },
      columns: [
        {
          title: "姓名",
          dataIndex: "name",
        },
      {
          title: "账户",
          dataIndex: "account",
        },
        {
        title: "微信号",
        dataIndex: "we_chat",
        },
        {
          title: "部门",
          dataIndex: "dept_name",
        },
        {
          title: "职务",
          dataIndex: "role_name",
          width: '120px'
        },
        {
          title: "性别",
          dataIndex: "sex_s",
          width: '80px'
        },
        {
          title: "状态",
          dataIndex: "status_s",
          width: '80px'
        },
        {
          title: "登录次数",
          dataIndex: "times_login",
          width: '100px'
        },
        {
          title: "最后登录时间",
          dataIndex: "time_last",
        },
        {
          title: '数据权限',
          dataIndex: 'authData',
          width: '160px',
          ellipsis: true
        },
        {
          title: '操作',
          dataIndex: 'operation',
          ellipsis: true
        }
      ],
      dataSource: [],
      saveModal: false,
      checkData: '',
      uid: '',
      role: [],
      title: "添加",
    });
    const clickInit = () => {
      formSelect.name = null
      formSelect.account = null
      formSelect.dept_id = null
      state.pagination.current = 1
      state.pagination.pageSize = 10
      queryList()
    };
    
    // 获取列表数据
    const queryList = () => {
      // 获取列表数据
      formSelect['current'] = state.pagination.current
      formSelect['pageSize'] = state.pagination.pageSize
      UserLists(formSelect).then((res) => {
        state.dataSource = res.lists.data;
        state.role = res.role;
        state.pagination.total = res.lists.total;
      });
    }
    queryList()
    deptListAll().then((res) => {
      if (res) {
        state.deptListAllData = getDeptAllList(res);
      }
    });

    const ShowModal = (e, data) => {
      state.saveModal = true;
      if (e == 1) { // 新增
          formState.uid = 0;
          formState.name = "";
          formState.account = "";
          formState.password = "";
          formState.we_chat = "";
          formState.sex = 1;
          formState.role_id = "0";
          formState.dept_id = "";
          formState.status = 1;
          state.title = "添加";
      } else if (e == 2) { // 编辑
        formState.uid = data.uid;
        formState.name = data.name;
        formState.dept_id = data.dept_id
        formState.account = data.account;
        formState.password = "";
        formState.we_chat = data.we_chat;
        formState.sex = data.sex;
        formState.role_id = data.role_id;
        formState.status = data.status;
        state.title = "修改";
      }
    };
    const OkModel = () => {
      // 先验证
      // 提交服务器
      if (!formState.account) {
        message.error("请输入账户", 1);
        return
      }
      if (formState.role_id == 0) {
        message.error("请选择部门", 1);
        return
      }

      UserSave(formState).then((res) => {
        if (res) {
          state.saveModal = false;
          message.success( state.title + '成功', 1);
          queryList()
        }
      });
    };
    const Del = (e) => {
      Modal.confirm({
        title: "警告",
        content: "删除后不能恢复，确定删除吗？",
        okText: "确认",
        cancelText: "取消",
        onOk() {
          UserDel({ uid: e.uid }).then((res) => {
            if (res) {
              message.success('删除成功', 1);
              state.saveModal = false;
              queryList()
            }
          });
        }
      });
    };
    
    const handleChange = (value) => {
      formState.dept_id = value
    };
    const changeDept = (value) => {
      formSelect.dept_id = value
    };

    const setBusinessAuth = (e) => {
      state.checkData = e.business
      state.uid = e.uid
      state.visible = true
      state.titleVal = '设置业务权限'
    }

    const closeComponent = () => {
      state.checkData = ''
      state.uid = ''
      state.titleVal = ''
      state.visible = false
    }
    return {
      closeComponent,
      setBusinessAuth,
      changeDept,
      handleChange,
      clickInit,
      formSelect,
      queryList,
      ...toRefs(state),
      formState,
      ShowModal,
      OkModel,
      Del
    };
  }
};
</script>
<style scoped>
  .detailBtn{
    margin-right: 15px;
  }
  ::v-deep .ant-select{
    width: 250px !important;
  }
</style>