<template>
    <div style="margin-bottom: 16px">
        <a-button type="primary" @click="ShowModal(1)">
            <template #icon>
                <component :is="$antIcons['PlusOutlined']" />
            </template>
            添加
        </a-button>
        <a-button type="primary" @click="ShowModal(2)" style="margin-left: 10px">
            <template #icon>
                <component :is="$antIcons['EditOutlined']" />
            </template>
            修改
        </a-button>
        <a-button type="primary" @click="Del" danger style="margin-left: 10px">
            <template #icon>
                <component :is="$antIcons['DeleteOutlined']" />
            </template>
            删除
        </a-button>
    </div>
    <a-modal v-model:visible="saveModal" width="100%" :title="title" wrapClassName="full-modal" okText="确定" okType="primary" cancelText="取消" @ok="OkModel" :confirm-loading="confirmLoading">
        <a-form :model="formState" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
            <a-form-item label="输入框" :rules="[{ required: true, message: '请输入' }]">
                <a-input v-model:value="formState.test_input" placeholder="请输入"/>
            </a-form-item>
            <a-form-item label="富文本">
                <a-textarea v-model:value="formState.test_rich" placeholder="请输入富文本" :rows="4" />
            </a-form-item>
            <a-form-item label="图片">
                <a-upload
                    name="file"
                    list-type="picture-card"
                    :show-upload-list="false"
                    :data="{Authorization:token}"
                    :action="upload_img"
                    @change="handleChange"
                >
                    <img v-if="formState.test_img_s" :src="formState.test_img_s" alt="test_img" style="width:80px;height:80px"  />
                    <div v-else>
                        <plus-outlined></plus-outlined>
                        <div>上传</div>
                    </div>
                </a-upload>
            </a-form-item>
            <a-form-item label="多图片">
                <a-upload
                        v-model:file-list="imgList"
                        list-type="picture-card"
                        :data="{Authorization:token}"
                        :action="upload_img"
                    >
                    <div v-if="imgList.length < 8">
                        <plus-outlined />
                        <div style="margin-top:8px">上传</div>
                    </div>
                </a-upload>
            </a-form-item>
            <a-modal :visible="previewVisible" @cancel="handleCancel" :footer="null">
                <img alt="example" style="width: 100%" :src="previewImage" />
            </a-modal>
            <a-form-item label="参照">
                <a-select ref="select" v-model:value="formState.test_reference">
                    <a-select-option :value="1">开启</a-select-option>
                    <a-select-option :value="0">关闭</a-select-option>
                </a-select>
            </a-form-item>
            <a-form-item label="年月日时分秒 时间戳">
                <a-date-picker format="YYYY-MM-DD HH:mm:ss" v-model:value="formState.test_time" showToday show-time />
            </a-form-item>
            <a-form-item label="年月日">
                <a-date-picker format="YYYY-MM-DD" v-model:value="formState.test_data" showToday />
            </a-form-item>
            <a-form-item label="年月日时分秒">
                <a-date-picker format="YYYY-MM-DD HH:mm:ss" v-model:value="formState.test_datatime" showToday show-time />
            </a-form-item>
            <a-form-item label="url">
                <a-input v-model:value="formState.test_url" placeholder="请输入网址"/>
            </a-form-item>
            <a-form-item label="单选框">
                <a-radio-group v-model:value="formState.test_radio">
                    <a-radio :value="1">启用</a-radio>
                    <a-radio :value="0">关闭</a-radio>
                </a-radio-group>
            </a-form-item>
            <a-form-item label="富文本">
                <Toolbar
                    style="border-bottom: 1px solid #ccc"
                    :editor="editorRef"
                    :defaultConfig="toolbarConfig"
                    :mode="mode"
                />
                <Editor
                    style="height: 500px; overflow-y: hidden;"
                    v-model="formState.test_rich_editor"
                    :defaultConfig="editorConfig"
                    :mode="mode"
                    @onCreated="handleCreated"
                />
            </a-form-item>
        </a-form>
    </a-modal>
    <a-table :dataSource="dataSource" :columns="columns" bordered :rowSelection="rowSelection">
        <template #bodyCell="{ column, record }">
            <template v-if="column.dataIndex == 'test_url'">
                <a target="_blank" :href="record.test_url">
                    {{ record.test_url }}
                </a>
            </template>
            <template v-if="column.dataIndex == 'test_img_s'">
                <a-image
                    v-if="record.test_img"
                    :width="80"
                    :height="80"
                    :src="record.test_img_s"
                />
            </template>
            <template v-if="column.dataIndex == 'test_img_many_s'">
                <a-image-preview-group v-if="record.test_img_many">
                    <a-image :width="80" :height="80" v-for="(item, index) in record.test_img_many_s" :key="index" :src="item.url" />
                </a-image-preview-group>
            </template>
        </template>
    </a-table>
</template>
<script>
import { reactive, toRefs, shallowRef } from "vue";
import { TestLists, TestSave, TestDel } from "@/network/bewtest.js";
import { message, Modal } from "ant-design-vue";
import dayjs from 'dayjs';

import '@wangeditor/editor/dist/css/style.css';
import { Editor, Toolbar } from '@wangeditor/editor-for-vue';
export default {
    components: {
        Editor,
        Toolbar
    },
    setup() {
        // form表单的默认值
        const formState = reactive({
            test_id: 0,
            test_input: "",
            test_rich: "",
            test_img: "",
            test_img_s : "",
            test_img_many: "",
            test_reference: 1,
            test_time: "",
            test_data: "",
            test_datatime: "",
            test_url: "",
            test_radio: 1,
            test_rich_editor : "",
        });
        // 管理员token，上传图片接口需要
        const token = window.localStorage.getItem("token");
        const state = reactive({
            // 列表头：标题、自定名
            columns: [
                {
                    title: "ID",
                    dataIndex: "test_id",
                },
                {
                    title: "文本",
                    dataIndex: "test_input",
                },
                {
                    title: "富文本",
                    dataIndex: "test_rich",
                },
                {
                    title: "图片(点击放大)",
                    dataIndex: "test_img_s",
                },
                {
                    title: "多图(点击放大)",
                    dataIndex: "test_img_many_s",
                },
                {
                    title: "参照",
                    dataIndex: "test_reference_s",
                },
                {
                    title: "年月日时分秒 时间戳",
                    dataIndex: "test_time_s",
                },
                {
                    title: "年月日",
                    dataIndex: "test_data",
                },
                {
                    title: "年月日时分秒",
                    dataIndex: "test_datatime",
                },
                {
                    title: "网址",
                    dataIndex: "test_url",
                },
                {
                    title: "单选框",
                    dataIndex: "test_radio_s",
                }
            ],
            // 列表数据
            dataSource: [],
            // 添加和修改，弹框
            saveModal: false,
            // 确定按钮，loading
            confirmLoading: false,
            // 行选择（单选）
            rowSelection: {
                type: "radio",
                onChange(selectedRowKeys, selectedRows) {
                    state.selectData = selectedRows[0];
                },
            },
            // 添加和修改：标题
            title: "添加",
            // 添加和修改：提示消息
            msg: "添加成功",
            // 选中的行数据
            selectData: [],

            // 图片弹框放大
            previewVisible : false,
            previewImage : "",
            // 图片列表
            imgList : [],
            // 图片上传地址
            upload_img : "http://admin-thinkphp-antd-vue-learning-api.ouyangke.com/index.php/admin/Index/upload_img",
            

            // 富文本
            mode: 'default',
        });
        // 获取列表数据
        TestLists().then((res) => {
            state.dataSource = res.lists;
        });
        // 添加和修改，弹框
        const ShowModal = (e) => {
            if (e == 1) {
                state.saveModal = true;
                state.imgList = [];

                formState.test_id = 0;
                formState.test_input = "";
                formState.test_rich = "";
                formState.test_rich_editor = "";
                formState.test_img = "";
                formState.test_img_s = "";
                formState.test_reference = 1;
                formState.test_time = "";
                formState.test_data = "";
                formState.test_datatime = "";
                formState.test_url = "";
                formState.test_radio = "";
                state.title = "添加";
                state.msg = "添加成功";
            } else if (e == 2) {
                if (state.selectData.test_id == undefined) {
                    message.error("请输入一条记录", 1);
                } else {
                    state.saveModal = true;
                    if(state.selectData.test_img_many_s){
                        state.imgList = state.selectData.test_img_many_s;
                    }else{
                        state.imgList = [];
                    }
                    formState.test_id = state.selectData.test_id;
                    formState.test_input = state.selectData.test_input;
                    formState.test_rich = state.selectData.test_rich;
                    formState.test_rich_editor = state.selectData.test_rich_editor;
                    formState.test_img = state.selectData.test_img;
                    formState.test_img_s = state.selectData.test_img_s;
                    formState.test_reference = state.selectData.test_reference;
                    formState.test_time = dayjs(state.selectData.test_time_s, 'YYYY/MM/DD HH:mm:ss') ;
                    formState.test_data = dayjs(state.selectData.test_data, 'YYYY/MM/DD') ;
                    formState.test_datatime = dayjs(state.selectData.test_datatime, 'YYYY/MM/DD HH:mm:ss') ;
                    formState.test_url = state.selectData.test_url;
                    formState.test_radio = state.selectData.test_radio;

                    state.title = "修改";
                    state.msg = "修改成功";
                }
            }
        };

        // 添加和修改，提交按钮
        const OkModel = () => {
            formState.test_img_many = "";
            for(let i=0;i<state.imgList.length;i++){
                if(!state.imgList[i].response){
                    formState.test_img_many += state.imgList[i].data + ";";
                }else{
                    formState.test_img_many += state.imgList[i].response.data + ";";
                }
            }

            state.confirmLoading = true;
            // 先验证
            // 提交服务器
            TestSave(formState).then((res) => {
                if(res != 1){
                    message.success(state.msg, 1);
                    setTimeout(() => {
                        // 重新请求列表
                        TestLists().then((res) => {
                            state.dataSource = res.lists;
                            state.saveModal = false;
                            state.confirmLoading = false;
                            // 1、第一种方法，刷新页面
                            // location.reload();
                            // 2、第二种方法，刷新页面
                            //   router.go(0);
                        });
                    }, 1000);
                }
                // 2、接口返回数据，重新加载
                if (state.selectData.test_id != undefined) {
                    state.selectData = res;
                }
            });
        };
        // 删除
        const Del = () => {
            if (state.selectData.test_id == undefined) {
                message.error("请输入一条记录", 1);
            } else {
                Modal.confirm({
                    title: "警告",
                    content: "删除后不能恢复，确定删除吗？",
                    okText: "确认",
                    cancelText: "取消",
                    onOk() {
                        TestDel({ test_id: state.selectData.test_id }).then((res) => {
                            if(res != 1){
                                message.success("删除成功", 1);
                                TestLists().then((res) => {
                                    state.dataSource = res.lists;
                                });
                                state.selectData = [];
                            }
                        });
                    }
                });
            }
        };
        // 图片上传后处理
        const handleChange = info => {
            if (info.file.status === "done") {
                // Get this url from response in real world.
                if(info.file.response.code == 0){
                    formState.test_img = info.file.response.data;
                    formState.test_img_s = info.file.response.url;
                }else{
                    message.error(info.file.response.url.msg);
                }
            }else if (info.file.status === "error") {
                message.error("上传失败");
            }
        };

        // 取消图片放大
        const handleCancel = () => {
            state.previewVisible = false;
        };

        // 编辑器实例，必须用 shallowRef
        const editorRef = shallowRef();
        const toolbarConfig = {}
        // 编辑器配置
        const editorConfig = {
            placeholder: '请填写内容',
            // 所有的菜单配置，都要在 MENU_CONF 属性下
            MENU_CONF: {
                // 配置上传图片
                uploadImage: {
                    // 上传名称，默认值 'wangeditor-uploaded-image'
                    fieldName: 'file',
                    // 单个文件的最大体积限制，默认为 2M
                    maxFileSize: 1 * 1024 * 1024, // 1M
                    // 服务端地址
                    server: state.upload_img + '_s',
                    // 自定义上传参数，例如传递验证的 token 等。
                    meta: {
                        Authorization : token
                    },
                },
            }
        }
        const handleCreated = (editor) => {
            editorRef.value = editor // 记录 editor 实例，重要！
        }

        return {
            ...toRefs(state),
            formState,
            ShowModal,
            OkModel,
            Del,
            handleChange,
            token,
            handleCancel,

            editorRef,
            toolbarConfig,
            editorConfig,
            handleCreated
        };
    }
};
</script>