<template>
    <div style="margin-bottom: 16px">
      <a-button type="primary" @click="ShowModal(1)">
        <template #icon>
          <component :is="$antIcons['PlusOutlined']" />
        </template>
        添加
      </a-button>
    </div>
    <a-table :columns="columns" :dataSource="dataSource" :pagination="false" rowKey="name">
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex == 'operation'">
          <a class="detailBtn" @click="ShowModal(2, record)">编辑</a>
          <a class="detailBtn" @click="Del(record)">删除</a>
        </template>
      </template>
    </a-table>

    <a-modal v-model:visible="saveModal" width="600px" :title="title" wrapClassName="full-modal" okText="确定" okType="primary" cancelText="取消" @cancel="cancelModel" @ok="OkModel">
      <a-form :model="formState" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
        <a-form-item label="部门名称" :rules="[{ required: true, message: '请输入部门名称' }]">
          <a-input v-model:value="formState.name" placeholder="请输入部门名称" />
        </a-form-item>
        <a-form-item label="上级部门">
          <a-select
            v-model="formState.parent_id"
            show-search
            placeholder="搜索上级部门"
            :options="deptListAllData"
            :field-names="{ label: 'full_name', value: 'id'}"
            :filter-option="filterOption"
            @change="handleChange"
          ></a-select>
        </a-form-item>
        <a-form-item label="排序">
          <a-input type="number" v-model:value="formState.sort" placeholder="请输入排序" />
        </a-form-item>
        <a-form-item label="参与业绩统计">
          <a-switch v-model:checked="formState.stat" checked-children="是" un-checked-children="否" />
        </a-form-item>
      </a-form>
    </a-modal>
  </template>
  <script>
  import { reactive, toRefs } from "vue";
  import { deptLists, deptSave, deptDel, deptListAll } from "@/network/bewadmin.js";
  import { message, Modal } from "ant-design-vue";
  import { getDeptAllList } from '@/utils/utils.js';
  export default {
    setup() {
      const formState = reactive({
        parent_id: null,
        name: null,
        full_id: null,
        full_name: null,
        sort: null,
        stat: null,
      });
      const state = reactive({
        deptListAllData: [],
        columns:[
          {
            key: 'name',
            title: "部门名称",
            dataIndex: "name",
          },
          {
            key: 'stat',
            title: "参与业绩统计",
            dataIndex: "stat",
          },
          {
            key: 'sort',
            title: "排序",
            dataIndex: "sort",
            width: '100px'
          },
          {
            key: 'operation',
            title: '操作',
            dataIndex: 'operation',
            ellipsis: true
          },
        ],
        dataSource: [],
        saveModal: false,
        title: "添加",
        msg: "添加成功"
      });
      // 获取列表数据
      const queryList = () => {
        deptLists().then((res) => {
          if (res) {
            state.dataSource = res;
          }
        });
      };
      queryList()
      const ShowModal = (e, data) => {
        state.saveModal = true;
        if (state.deptListAllData.length == 0) {
          deptListAll().then((res) => {
            if (res) {
              state.deptListAllData = getDeptAllList(res);
              if (e == 2) {
                formState.parent_id = data.parent_id
                state.title = "修改";
                state.msg = "修改成功";
              }
            }
          });
        }
        if (e == 1) {
          state.title = "添加";
          state.msg = "添加成功";
        } else if (e == 2) {
          formState.parent_id = data.parent_id
          formState.name = data.name
          formState.full_id = data.full_id
          formState.full_name = data.full_name
          formState.sort = data.sort
          formState.stat = data.stat == 1
          state.title = "修改";
          state.msg = "修改成功";
        }
      };
      const OkModel = () => {
        // 提交服务器
        if (!formState.name) {
            message.error("请输入部门名称", 1);
            return false;
        }

        let obj = formState
        obj.stat = formState.stat ? 1 : 0
        deptSave(obj).then((res) => {
          if (res) {
            message.success( state.title + '成功', 1);
            cancelModel()
            queryList()
          }
        });
      };
      const cancelModel = () => {
        state.saveModal = false;
        formState.parent_id = null
        formState.name = null
        formState.full_id = null
        formState.full_name = null
        formState.sort = null
        formState.stat = null
      }
      const Del = (data) => {
        Modal.confirm({
          title: "警告",
          content: "删除后不能恢复，确定删除吗？",
          okText: "确认",
          cancelText: "取消",
          onOk() {
            deptDel({ id: data.id }).then((res) => {
              if (res) {
                message.success("删除成功", 1);
                queryList()
              }
            });
          },
        });
      };
      const filterOption = (input, option) => {
        return option.name.toLowerCase().indexOf(input.toLowerCase()) >= 0;
      };
      const handleChange = (value) => {
        formState.parent_id = value
        getFull(value)
      };
      const getFull = (id) => {
        state.deptListAllData.forEach(ele => {
          if (ele.id == id) {
            formState.full_name = ele.full_name
            if (ele.full_id && ele.full_id != 0){
              formState.full_id = ele.full_id + ',' + ele.id
            } else {
              formState.full_id = ele.id
            }
          }
        })
      }
      return {
        cancelModel,
        getFull,
        handleChange,
        filterOption,
        queryList,
        ...toRefs(state),
        formState,
        ShowModal,
        OkModel,
        Del
      };
    }
  };
  </script>
  <style scoped>
    .detailBtn{
      margin-right: 15px;
    }
  </style>