import { request } from "./request.js";
 /**
 * 一阶客户添加或保存
* @param id 客户id  编辑时传入
 * @param corp_name 企业名称
 * @param classify_label 客户分类
 * @param name 客户姓名
 * @param mobile 手机号码 必填
 * @param phone 手机号码
 * @param money_amount 资金需求
 * @param capital_use_label 客戶来源
 * @param classify_label 客户分类
 * @param interest_range 利息区间
 * @param source_label 客户来源
 * @param gender 客户性别
 * @param know_cost 知晓费用
 * @param add_wechat 已加微信
 * @param remark 备注信息
 */
export function oneCustomerAddOrEdit(data = {}) {
    return request({
        url: "Business/oneCustomerAddOrEdit",
        method: "post",
        data
    });
}
 /**
 * 我的客户
 * @param corp_name 企业名称
 * @param status 状态
 * @param name 客户姓名
 * @param add_wechat 是否加微信
 * @param money_amount 资金需求(万) 区间
 * @param corp_province 省
 * @param corp_city 市
 * @param corp_county 区/县
 * @param industry_classify_label 所属行业
 * @param pay_tax_level_label 纳税等级
 * @param legal 是否法人
 * @param protect 是否保护  1是 0否
 * @param current 分页起始位置
 * @param pageSize 分页大小
 */
export function customerLists(data = {}) {
   return request({
       url: "Business/customerLists",
       method: "post",
       data
   });
}
/**
 * 保护客户 解除客户
 * @param id 客户id
 * @param corp_name 公司名称
 * @param protect 当前保护状态 1是 0否
 */
export function protectOrRelieveCustomer(data = {}) {
  return request({
      url: "Business/protectOrRelieveCustomer",
      method: "post",
      data
  });
}
