<script>
import { reactive, watch, toRefs  } from "vue";
import { useStore } from "vuex";
import { customerDetail} from "@/network/business.js";
import FollowRecord from '@/components/FollowRecord.vue'
import { getCurrentInstance } from "@vue/runtime-core";  // 引入getCurrentInstance
import CustomerOne from '@/components/CustomerOne.vue'
import CustomerTwo from '@/components/CustomerTwo.vue'
import CustomerThree from '@/components/CustomerThree.vue'

export default {
  name: "CustomerDetail",
  components: {
    CustomerThree,
    CustomerTwo,
    CustomerOne,
    FollowRecord
  },
  props: {
    visible: {
      type: Boolean,
      required: false
    },
    customerId: {
      type: Number,
      default: 0
    }
  },
  setup(props, context) {
    let $bus = getCurrentInstance().appContext.config.globalProperties.$bus; // 声明$bus
    const { visible, customerId } = toRefs(props);
    const store = useStore();
    $bus.on("refreshCustomerList", () => {
      getDetail()
    });

    const state = reactive({
      titleVal: '',
      visibleOne: false,
      visibleTwo: false,
      visibleThree: false,
      showModel: false,
      customer: {},
      customerObj: {}
    })
    watch(() => visible.value, (newVal) => {
      state.showModel = newVal
      getDetail()
    },{deep: true})
    const getDetail = () => {
      if (!customerId.value) {
        return
      }
      let data = {
        id: customerId.value
      }
      customerDetail(data).then((res) => {
        if (res) {
          state.customer = res
          state.customer.id_card_front_img_val = process.env.VUE_APP_BASE + res.id_card_front_img
          state.customer.id_card_back_img_val = process.env.VUE_APP_BASE + res.id_card_back_img
          state.customer.corp_credit_img_val = process.env.VUE_APP_BASE + res.corp_credit_img
          state.customer.corp_licence_img_val = process.env.VUE_APP_BASE + res.corp_licence_img

          let customerStatus = store.state.customerStatus
          customerStatus.forEach(ele => {
            if(ele.value == res.status) {
              state.customer.status_name = ele.label
            }
          })
        }
      });
    }
    const cancelModel = () => {
      context.emit('closeBox')
    }
    
    const showStatusModel = (e) => {
      state.customerObj = e.obj
      if (e.status == 'ONE_STAGE') {
        state.visibleOne = true
        state.titleVal = '编辑一阶'
      } else if (e.status == 'TWO_STAGE') {
        state.titleVal = '编辑二阶'
        state.visibleTwo = true
      } else if (e.status == 'THREE_STAGE') {
        state.titleVal = '编辑三阶'
        state.visibleThree = true
      }
    }
    const handleOneSave = () => {
      let data = {
        status: 'ONE_STAGE',
        obj: state.customer
      }
      showStatusModel(data)
    }
    const handleTwoSave = () => {
      let data = {
        status: 'TWO_STAGE',
        obj: state.customer
      }
      showStatusModel(data)
    }
    const handleThreeSave = () => {
      let data = {
        status: 'THREE_STAGE',
        obj: state.customer
      }
      showStatusModel(data)
    }
    const closeComponent = () => {
      state.customerObj = {}
      state.visibleOne = false
      state.visibleTwo = false
      state.visibleThree = false
    }
    return {
      closeComponent,
      handleOneSave,
      handleTwoSave,
      handleThreeSave,
      cancelModel,
      ...toRefs(state)
    };
  } 
};
</script>
