<template>
    <div>
      <a-form
        :model="formSelect"
        layout="inline"
        class="prjFormBox"
      >
        <a-form-item
          label="表名"
          name="table_name"
        >
          <a-input v-model:value="formSelect.table_name" placeholder="请输入表名"/>
        </a-form-item>
        <a-form-item
          label="列名"
          name="column_name"
        >
          <a-input v-model:value="formSelect.column_name" placeholder="请输入列名"/>
        </a-form-item>
        <a-form-item
          label="名称"
          name="the_key"
        >
          <a-input v-model:value="formSelect.the_key" placeholder="请输入名称"/>
        </a-form-item>
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
            <a-button type="primary" html-type="submit" @click="queryList">查询</a-button>
        </a-form-item>
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
            <a-button @click="clickInit">重置</a-button>
        </a-form-item>
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
            <a-button type="primary" html-type="submit" @click="clickAdd">新增</a-button>
        </a-form-item>
      </a-form>
      <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
        <template #bodyCell="{ column, record }">
          <template v-if="column.dataIndex === 'operation'">
            <a class="detailBtn"  @click="clickEdit(record)">编辑</a>
            <a class="detailBtn" @click="Del(record)">删除</a>
          </template>
        </template>
      </a-table>
      <a-modal v-model:visible="saveModal" width="400px" :title="modelTitle" wrapClassName="full-modal" okText="确定" okType="primary" cancelText="取消" @ok="OkModel" @cancel="closeModel">
          <a-form :model="formData" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
              <a-form-item label="表名" :rules="[{ required: true, message: '请输入表名' }]">
                  <a-input v-model:value="formData.table_name" placeholder="请输入表名"/>
              </a-form-item>
              <a-form-item label="列名" :rules="[{ required: true, message: '请输入列名' }]">
                  <a-input v-model:value="formData.column_name" placeholder="请输入列名"/>
              </a-form-item>
              <a-form-item label="名称" :rules="[{ required: true, message: '请输入名称' }]">
                  <a-input v-model:value="formData.the_key" placeholder="请输入名称"/>
              </a-form-item>
              <a-form-item label="值">
                  <a-input v-model:value="formData.the_value" placeholder="请输入值"/>
              </a-form-item>
              <a-form-item label="顺序">
                  <a-input v-model:value="formData.sort" placeholder="请输入顺序"/>
              </a-form-item>
              <a-form-item label="备注">
                  <a-input v-model:value="formData.remark" placeholder="请输入备注"/>
              </a-form-item>
          </a-form>
      </a-modal>
    </div>
  </template>
  <script>
  import { reactive, toRefs } from "vue";
  import { EnumList, EnumAddOrEdit, EnumDel} from "@/network/enum.js";
  import { message, Modal } from "ant-design-vue";
  export default {
    components: {},
    setup() {
      const state = reactive({
        saveModal: false,
        modelTitle: '添加枚举',
        pagination: {
          total: 0,
          current: 1,
          pageSize: 10,
          onChange: (e) => {
            state.pagination.current = e
            queryList()
          }
        },
        columns: [
            {
                title: "表名",
                dataIndex: "table_name",
                ellipsis: true
            },
            {
                title: "列名",
                dataIndex: "column_name",
                ellipsis: true
            },
            {
                title: "名称",
                dataIndex: "the_key",
                ellipsis: true
            },
            {
                title: "值",
                dataIndex: "the_value",
                ellipsis: true
            },
            {
                title: "排序",
                dataIndex: "sort",
                ellipsis: true
            },
            {
                title: "备注",
                dataIndex: "remark",
                ellipsis: true
            },
            {
              title: '操作',
              dataIndex: 'operation',
              ellipsis: true
            },
        ],
        dataSource: [],
      });
      let formData = reactive({
        table_name: null,
        column_name: null,
        the_key: null,
        the_value: null,
        sort: null,
        remark: null,
      });
      let formSelect = reactive({
        table_name: null,
        column_name: null,
        the_key: null,
      });
      const clickAdd = () => {
        formData.table_name = null
        formData.column_name = null
        formData.the_key = null
        formData.the_value = null
        formData.sort = null
        formData.remark = null
        state.modelTitle = '添加枚举'
        state.saveModal = true
      }
      const clickEdit= (e) => {
        formData.table_name = e.table_name
        formData.column_name = e.column_name
        formData.the_key = e.the_key
        formData.the_value = e.the_value
        formData.sort = e.sort
        formData.remark = e.remark
        formData.id = e.id
        state.modelTitle = '编辑枚举'
        state.saveModal = true
      }
      const clickInit = () => {
        formSelect.table_name = null
        formSelect.column_name = null
        formSelect.the_key = null
        formData = reactive({
          table_name: null,
          column_name: null,
          the_key: null,
          the_value: null,
          sort: null,
          remark: null,
        })
        state.pagination.current = 1
        state.pagination.pageSize = 10
        queryList()
      };
      const queryList = () => {
        // 获取列表数据
        formSelect['current'] = state.pagination.current
        formSelect['pageSize'] = state.pagination.pageSize
        EnumList(formSelect).then((res) => {
          if (res) {
            state.dataSource = res.data;
            state.pagination.total = res.total;
          }
        });
      };
      queryList()
      const OkModel = () => {
        if (!formData.table_name) {
          message.error("请输入表名", 1);
          return
        }
        if (!formData.column_name) {
          message.error("请输入列名", 1);
          return
        }
        if (!formData.the_key) {
          message.error("请输入名称", 1);
          return
        }
        EnumAddOrEdit(formData).then((res) => {
          if (res) {
            if (state.modelTitle == '编辑枚举') {
              message.success("编辑成功", 1);
            } else {
              message.success("添加成功", 1);
            }
            clickInit()
            closeModel()
          }
        })
      }
      const closeModel = () => {
        formData.table_name = null
        formData.column_name = null
        formData.the_key = null
        formData.the_value = null
        formData.sort = null
        formData.remark = null
        formData.id = null
        state.saveModal = false
        state.modelTitle = '添加成功'
      }
      const Del = (e) => {
        Modal.confirm({
          title: "警告",
          content: "删除后不能恢复，确定删除吗？",
          okText: "确认",
          cancelText: "取消",
          onOk() {
            let data = {
              id:e.id
            }
            EnumDel(data).then((res) => {
              if (res) {
                message.success("删除成功", 1);
                queryList()
              }
            });
          }
        });
      };

      return {
        clickAdd,
        Del,
        OkModel,
        queryList,
        clickInit,
        clickEdit,
        formData,
        formSelect,
        closeModel,
        ...toRefs(state)
      };
    }
  };
  </script>
  <style scoped>
    .detailBtn{
      margin-right: 15px;
    }
  </style>