import { request } from "./request.js";
export function openIndex(data = {}) {
    return request({
        url: "Open/index",
        method: "post",
        data
    });
}

export function lockList(data = {}) {
    return request({
        url: "Open/lockList",
        method: "post",
        data
    });
}

export function lockCustomer(data = {}) {
    return request({
        url: "Open/lock",
        method: "post",
        data
    });
}