<template>
    <a-form :model="formState" :label-col="{ span: 5 }" :wrapper-col="{ span: 14 }">
        <a-form-item label="账户">
            <a-input :value="formState.account" disabled />
        </a-form-item>
        <a-form-item label="姓名">
            <a-input v-model:value="formState.name" placeholder="请输入姓名" />
        </a-form-item>
        <a-form-item label="微信号">
            <a-input v-model:value="formState.we_chat" placeholder="请输入微信号" />
        </a-form-item>
        <a-form-item label="密码">
            <a-input v-model:value="formState.password" placeholder="不修改，不填写"/>
        </a-form-item>
        <a-form-item label="性别">
            <a-radio-group v-model:value="formState.sex">
                <a-radio :value="1">男</a-radio>
                <a-radio :value="2">女</a-radio>
            </a-radio-group>
        </a-form-item>
        <a-form-item style="text-align: center" :wrapper-col="{ span: 14, offset: 4 }">
            <a-button type="primary" @click="onSubmit">保存</a-button>
        </a-form-item>
    </a-form>
</template>
<script>
import { reactive } from "vue";
import { UserInfo } from "@/network/index.js";
import { message } from "ant-design-vue";
import { useRouter } from "vue-router";
export default {
    setup() {
        const user = JSON.parse(window.localStorage.getItem("user"));
        const formState = reactive({
            account: user.account,
            name: user.name,
            we_chat: user.we_chat,
            sex: user.sex,
            password: ""
        });
        const router = useRouter();
        const onSubmit = () => {
            UserInfo(formState).then((res) => {
                if (res != 1) {
                    window.localStorage.setItem("user", JSON.stringify(res));
                    message.success("修改成功", 1);
                    setTimeout(() => {
                        router.go(0);
                    }, 1000);
                }
            });
        };

        return {
            formState,
            onSubmit,
        };
    }
};
</script>