<template>
    <a-layout style="background:#fff;">
        <div class="container">
            <div style="text-align: center">
                <img src="@/assets/logo.jpg" alt="logo" />
                <h1>青鸟后台管理</h1>
            </div>
            <div class="main">
                <a-form :model="formState">
                    <a-form-item name="account" :rules="[{ required: true, message: '请输入账号' }]">
                        <a-input v-model:value="formState.account" placeholder="请输入账号">
                            <template #prefix>
                                <UserOutlined style="color: #1890ff"  :is="$antIcons['DownOutlined']" />
                            </template>
                        </a-input>
                    </a-form-item>
                    <a-form-item name="password" :rules="[{ required: true, message: '请输入密码' }]">
                        <a-input-password v-model:value="formState.password" placeholder="请输入密码">
                            <template #prefix>
                                <LockOutlined style="color: #1890ff"  :is="$antIcons['DownOutlined']" />
                            </template>
                        </a-input-password>
                    </a-form-item>
                    <img :src="formState.codeImg" @click="getVerify">
                    
                    <a-form-item name="code" :rules="[{ required: true, message: '请输入验证码' }]">
                        <a-input v-model:value="formState.code" placeholder="请输入验证码"></a-input>
                    </a-form-item>
                    <div class="login-form-wrap">
                        <a-form-item name="remember" no-style>
                            <a-checkbox v-model:checked="formState.remember">自动登录</a-checkbox>
                        </a-form-item>
                    </div>

                    <a-form-item style="text-align: center">
                        <a-button type="primary" html-type="submit" class="login-form-button" block @click="onSubmit">
                            登录
                        </a-button>
                    </a-form-item>
                </a-form>
            </div>
        </div>
    </a-layout>
</template>
<script>
import { reactive } from "vue";
import { Login, Verify } from "@/network/login.js";
import { useRouter } from "vue-router";
import { message } from "ant-design-vue";
export default {
    setup() {
        const formState = reactive({
            account: "",
            password: "",
            codeImg: null,
            code: null,
            uniqid: null,
            remember: true
        });

        const router = useRouter();
        const getVerify = () => {
            Verify().then((res) => {
                if(res){
                    formState.codeImg = res.data.src
                    formState.uniqid = res.data.uniqid
                }
            });
        }
        getVerify();

        const onSubmit = () => {
            Login(formState).then((res) => {
                if(res){
                    // 将token 保存到localStorage（本地数据库）里
                    window.localStorage.setItem("token", res.ticket);
                    window.localStorage.setItem("user", JSON.stringify(res));
                    message.success("登录成功", 1);
                    setTimeout(() => {
                        router.push("/index");
                    }, 1000);
                }
            });
        };

        return {
            getVerify,
            formState,
            onSubmit
        };
    }
};
</script>
<style>
.container {
    position: relative;
    width: 100%;
    /* min-height: 100%; */
    padding: 110px 0 144px;
    /* background-repeat: no-repeat;
    background-position: center 110px;
    background-size: 100%; */
}
.main {
    width: 368px;
    min-width: 260px;
    margin: 50px auto;
}
</style>
