<template>
    <a-form
      :model="formSelect"
      layout="inline"
      class="prjFormBox"
    >
      <a-form-item
        label="职务名称"
        name="role_name"
      >
        <a-input v-model:value="formSelect.role_name" placeholder="请输入账号"/>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button type="primary" html-type="submit" @click="queryList">查询</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button @click="clickInit">重置</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button type="primary" html-type="submit" @click="ShowModal(1)">新增</a-button>
      </a-form-item>
    </a-form>
    <a-modal v-model:visible="saveModal" width="600px" :title="title" wrapClassName="full-modal" okText="确定" okType="primary" cancelText="取消" @ok="OkModel" >
      <a-form :model="formState" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
        <a-form-item label="职务名称" :rules="[{ required: true, message: '请输入职务名称' }]">
          <a-input v-model:value="formState.role_name" placeholder="请输入职务名称"/>
        </a-form-item>
        <a-form-item label="状态">
          <a-radio-group  v-model:value="formState.status">
            <a-radio :value="1">启用</a-radio>
            <a-radio :value="0">关闭</a-radio>
          </a-radio-group>
        </a-form-item>
        <a-form-item label="权限">
          <a-checkbox-group  v-model:value="formState.menus">
          <div v-for="(item, index) in plainOptions" :key="index" :style="{ borderBottom: '1px solid #E9E9E9' }">
            <br v-if="index != 1" />
            <a-checkbox :value="item.mid">{{ item.label }}</a-checkbox>
            <div>
                <a-checkbox v-for="(items, indexs) in item.son" :key="indexs" :value="items.mid">{{ items.label }}</a-checkbox>
            </div>
            <br />
          </div>
          </a-checkbox-group>
        </a-form-item>
      </a-form>
    </a-modal>
    <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="false">
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'operation'">
          <a class="detailBtn"  @click="ShowModal(2, record)">编辑</a>
          <a class="detailBtn" @click="Del(record)">删除</a>
        </template>
      </template>
    </a-table>
</template>
<script>
import { reactive, toRefs } from "vue";
import { roleLists, roleSave, roleDel } from "@/network/bewadmin.js";
import { message, Modal } from "ant-design-vue";
export default {
  setup() {
    const formSelect = reactive({
      role_name: null,
    });
    const formState = reactive({
        role_id: 0,
        role_name: "",
        status: 1,
        menus: []
    });
    const state = reactive({
        columns: [
            {
                title: "职务名称",
                dataIndex: "role_name",
            },
            {
                title: "状态",
                dataIndex: "status_s",
            },
            {
                title: "添加时间",
                dataIndex: "time_add",
            },
            {
                title: '操作',
                dataIndex: 'operation',
                ellipsis: true
            }
        ],
        dataSource: [],
        saveModal: false,
        plainOptions: [],
        title: "添加职务",
        selectData: []
    });
    const clickInit = () => {
      formSelect.role_name = null
      queryList()
    };
    // 获取列表数据
    const queryList = () => {
      roleLists(formSelect).then((res) => {
        if (res) {
          state.dataSource = res.lists;
          state.plainOptions = res.menu;
        }
      });
    }
    queryList()
    const ShowModal = (e, data) => {
      if (e == 1) {
        state.saveModal = true;
        formState.role_id = 0;
        formState.role_name = "";
        formState.status = 1;
        formState.menus = [];
        state.title = "添加职务";
      } else if (e == 2) {
        state.saveModal = true;
        formState.role_id = data.role_id;
        formState.role_name = data.role_name;
        formState.status = data.status;
        formState.menus = data.rights;
        state.title = "修改职务";
      }
    };
    const OkModel = () => {
      // 提交服务器
      if (!formState.role_name) {
        message.error("请输入职务名称", 1);
      } else {
        roleSave(formState).then((res) => {
          if (res) {
            state.saveModal = false;
            message.success( state.title + '成功', 1);
            queryList()
          }
        });
      }
    };
    const Del = (e) => {
      Modal.confirm({
        title: "警告",
        content: "删除后不能恢复，确定删除吗？",
        okText: "确认",
        cancelText: "取消",
        onOk() {
          roleDel({ role_id: e.role_id }).then((res) => {
            if (res) {
              message.success('删除成功', 1);
              state.saveModal = false;
              queryList()
            }
          });
        }
      });
    };

    return {
      queryList,
      formSelect,
      ...toRefs(state),
      formState,
      ShowModal,
      OkModel,
      clickInit,
      Del
    };
  }
};
</script>
<style scoped>
  .detailBtn{
    margin-right: 15px;
  }
</style>