<template>
  <div class="upload-container">
    <a-upload
      v-model:file-list="fileList"
      list-type="picture-card"
      :maxCount="1"
      :customRequest="uploadImage"
      @preview="handlePreview"
    >
      <div  v-if="fileList.length < 1">
        <plus-outlined />
        <div style="margin-top: 8px">Upload</div>
      </div>
    </a-upload>
    <a-modal :visible="previewVisible" title="图片预览" :footer="null" @cancel="handleCancel">
      <img alt="example" style="width: 100%" :src="previewImage" />
    </a-modal>
  </div>
</template>

<script>
import { reactive,  toRefs  } from "vue";
import { uploadImg } from "@/network/utils.js";
import { getCurrentInstance } from "@vue/runtime-core";  // 引入getCurrentInstance
export default {
  name: "UpdateImg", // 上传图片
  components: {},
  props: {
    imgUrl: {
      default: '',
      type: String
    },
    source: {
      default: '',
      type: String
    }
  },
  setup(props) {
    const { imgUrl, source } = toRefs(props);
    let $bus = getCurrentInstance().appContext.config.globalProperties.$bus; // 声明$bus


    const state = reactive({
      fileList : [],
      previewVisible: false,
      previewImage: null
    })
    state.fileList = [{
      url: process.env.VUE_APP_BASE + imgUrl.value
    }]
    
    const uploadImage = (e) => {
      const formData = new FormData()
      formData.append('file', e.file)
      uploadImg(formData).then(res => {
        if (res) {
          state.fileList = [{
            url: process.env.VUE_APP_BASE + res
          }]
          let data = {
            type: source.value,
            url: res
          }
          $bus.emit("setUpdateImg", data)
        }
      })
    }
    const handlePreview = async file => {
      state.previewImage = file.url || file.preview;
      state.previewVisible = true;
    };
    const handleCancel = () => {
      state.previewVisible = false;
    };
    return {
      handleCancel,
      handlePreview,
      uploadImage,
      ...toRefs(state)
    };
  } 
};
</script>
