import { request } from "./request.js";
/**
 * 部门列表
*/
export function deptLists(data) {
    return request({
        url: "Bewadmin/deptLists",
        method: "post",
        data,
    });
}
/**
 * 同一级别展示全部部门列表
*/
export function deptListAll(data) {
    return request({
        url: "Bewadmin/deptListAll",
        method: "post",
        data,
    });
}


/**
 * 部门添加、修改
 * @param id 部门id 编辑传入
 * @param parent_id '父部门ID
 * @param full_id 父部门ID集
 * @param full_name 父部门名称集
 * @param name 部门名称
 * @param sort 部门内排序
 * @param stat 是否参与业绩统计  1是 0否
 */
export function deptSave(data) {
    return request({
        url: "Bewadmin/deptSave",
        method: "post",
        data,
    });
}
/**
 * 部门删除
 * @param id 部门id
 */
export function deptDel(data) {
    return request({
        url: "Bewadmin/deptDel",
        method: "post",
        data,
    });
}

