<template>
  <div>
    <a-form
      :model="formState"
      layout="inline"
      class="prjFormBox"
    >
      <a-form-item
        label="公司名称"
        name="corp_name"
      >
        <a-input v-model:value="formState.corp_name" allowClear placeholder="请输入公司名称"/>
      </a-form-item>
      <a-form-item
        label="法人"
        name="legal_name"
      >
        <a-input v-model:value="formState.legal_name" allowClear placeholder="请输入法人"/>
      </a-form-item>
      <a-form-item
        label="地区"
        name="address"
      >
        <a-input v-model:value="formState.address" allowClear placeholder="请输入地区"/>
      </a-form-item>

      <a-form-item
        label="所属行业"
        name="industry"
      >
        <a-input v-model:value="formState.industry" allowClear placeholder="请输入所属行业"/>
      </a-form-item>
 

      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
        <a-button type="primary" html-type="submit" @click="clickQuery">查询</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
        <a-button @click="clickInit">重置</a-button>
      </a-form-item>
    </a-form>
    
    <a-button type="primary" class="importData" @click="importData">
      <template #icon>
        <DownloadOutlined />
      </template>
      导入
    </a-button>

    <a-modal v-model:visible="showModel" title="导入私海" cancelText="取消" okText="确定" @ok="startImport">
      <a-upload
        @change="handleChange"
        @remove="removeFile"
        @beforeUpload="beforeUploadFile"
        :customRequest="customUpload"
        :max-count="1"
        :multiple="false"
        name="file"
      >
        <a-button>
          <upload-outlined></upload-outlined>
          上传数据
        </a-button>
      </a-upload>
      <a class="templateUrl" @click="dowloadTemplate">模板下载</a>
      <span class="tips">请上传exl文件, 每次上传最多不超过500条数据！</span>
    </a-modal>
    

    <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
      <template #bodyCell="{ column, record }">
        <template v-if="column.dataIndex === 'operation'">
          <a class="detailBtn" @click="clickShiftCustomer(record)">转入客户</a>
        </template>
      </template>
    </a-table>
    <customer-one :visible="visible" :customerObj="customerObj" :titleVal="转入客户"  @closeBox="onChildClick"></customer-one>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
import { lockList} from "@/network/open.js";
import { importExlTemplate} from "@/network/utils.js";
import { DownloadOutlined } from '@ant-design/icons-vue';
import { message } from 'ant-design-vue';
import CustomerOne from '@/components/CustomerOne.vue'
export default {
  components: {
    DownloadOutlined,
    CustomerOne
  },
  setup() {
    const state = reactive({
      customerObj: {},
      visible: false,
      exlFile: null,
      templateUrl: process.env.VUE_APP_BASE + 'example.xls',
      showModel: false,
      formState: {
        corp_name: null,
        legal_name: null,
        industry:  null,
        address: null,
      },
      pagination: {
        total: 0,
        current: 1,
        pageSize: 10,
        onChange: (e) => {
          state.pagination.current = e
          queryList()
        }
      },
      columns: [
          {
              title: "公司名称",
              dataIndex: "corp_name",
              width: '300px',
              ellipsis: true
          },
          {
              title: "法人",
              dataIndex: "legal_name",
              width: '100px',
              ellipsis: true
          },
          {
              title: "所属行业",
              dataIndex: "industry",
              ellipsis: true
          },
          {
              title: "地区",
              dataIndex: "address",
              ellipsis: true
          },
          {
              title: "查询次数",
              dataIndex: "query_num",
              width: '100px',
              ellipsis: true
          },
          {
              title: "系统添加时间",
              dataIndex: "create_time",
              width: '200px',
              ellipsis: true
          },
          {
            title: '操作',
            dataIndex: 'operation',
            ellipsis: true
          },
      ],
      dataSource: [],
    });
    const clickQuery = () => {
      queryList()
    };
    const clickInit = () => {
      state.formState.corp_name = null
      state.formState.legal_name = null
      state.formState.industry = null
      state.formState.address = null
      state.formState.current = null
      state.pagination.current = 1
      queryList()
    };
    const queryList = () => {
      // 获取列表数据
      state.formState['current'] = state.pagination.current
      state.formState['pageSize'] = state.pagination.pageSize
      state.dataSource = [];
      lockList(state.formState).then((res) => {
        if (res) {
          state.dataSource = res.data;
          state.pagination.total = res.total;
        }
      });
    };
    queryList()
    const importData = () => {
      state.showModel = true
    }
    const customUpload = () => {
      return;
    }
    const startImport = () => {
      if (!state.exlFile) {
        message.error('请先上传文件');
      }
      const formData = new FormData()
      formData.append('file', state.exlFile)
      importExlTemplate(formData).then(res => {
        if (res) {
          message.success('导入成功');
          state.showModel = false;
          clickInit()
        }
      })
    }
    const dowloadTemplate = () => {
      window.open(state.templateUrl)
    }
    const handleChange = (res) => {
      state.exlFile = res.file.originFileObj
      res.file.status = res.file.originFileObj ? "done" : "error"
    }
    const removeFile = () => {
      state.exlFile = null
    }
    const beforeUploadFile = () => {
      return false
    }
    const clickShiftCustomer = (record) => {
      state.customerObj = record
      state.visible = true
    }
    const onChildClick = () => {
      state.customerObj = {}
      state.visible = false
    }
    
    return {
      onChildClick,
      clickShiftCustomer,
      beforeUploadFile,
      removeFile,
      handleChange,
      customUpload,
      dowloadTemplate,
      startImport,
      importData,
      clickInit,
      queryList,
      clickQuery,
      ...toRefs(state)
    };
  }
};
</script>
<style scoped>
  .detailBtn{
    margin-right: 15px;
  }
  .importData{
    margin-bottom: 20px;
  }
  .tips{
    font-size: 12px;
    color: red;
    display: block;
    margin-top: 10px;
  }
  .templateUrl{
    margin-top: 15px;
    display: block;
  }
</style>