<template>
    <div>
      <a-form
        :model="formState"
        layout="inline"
        class="prjFormBox"
      >
        <a-form-item
          label="公司名称"
          name="corp_name"
        >
          <a-input v-model:value="formState.corp_name" placeholder="请输入公司名称"/>
        </a-form-item>
        <a-form-item
          label="法人"
          name="legal_name"
        >
          <a-input v-model:value="formState.legal_name" placeholder="请输入法人"/>
        </a-form-item>
        <a-form-item
          label="地区"
          name="address"
        >
          <a-input v-model:value="formState.address" placeholder="请输入地区"/>
        </a-form-item>
  
        <a-form-item
          label="所属行业"
          name="industry"
        >
          <a-input v-model:value="formState.industry" placeholder="请输入所属行业"/>
        </a-form-item>
        <a-form-item
          label="查询次数"
          name="start_query_num"
        >
          <a-input class="inputStyle" v-model:value="formState.start_query_num" placeholder="请输入开始查询次数"/>
        </a-form-item>
        <a-form-item>
          <a-input class="inputStyle" v-model:value="formState.end_query_num" placeholder="请输入结束查询次数"/>
        </a-form-item>
  
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button type="primary" html-type="submit" @click="clickQuery">查询</a-button>
        </a-form-item>
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button @click="clickInit">重置</a-button>
        </a-form-item>
      </a-form>
      <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
        <template #bodyCell="{ column}">
          <template v-if="column.dataIndex === 'operation'">
            <a class="detailBtn">转入客户</a>
          </template>
        </template>
      </a-table>
    </div>
  </template>
  <script>
  import { reactive, toRefs } from "vue";
  import { openIndex} from "@/network/open.js";
  export default {
    components: {},
    setup() {
      const state = reactive({
        formState: {
          corp_name: null,
          legal_name: null,
          industry:  null,
          start_query_num: null,
          end_query_num: null,
          address: null,
        },
        pagination: {
          total: 0,
          current: 1,
          pageSize: 10,
          onChange: (e) => {
            state.pagination.current = e
            queryList()
          }
        },
        columns: [
            {
                title: "公司名称",
                dataIndex: "corp_name",
                width: '300px',
                ellipsis: true
            },
            {
                title: "法人",
                dataIndex: "legal_name",
                width: '100px',
                ellipsis: true
            },
            {
                title: "所属行业",
                dataIndex: "industry",
                ellipsis: true
            },
            {
                title: "地区",
                dataIndex: "address",
                ellipsis: true
            },
            {
                title: "查询次数",
                dataIndex: "query_num",
                width: '100px',
                ellipsis: true
            },
            {
                title: "系统添加时间",
                dataIndex: "create_time",
                width: '200px',
                ellipsis: true
            },
            {
              title: '操作',
              dataIndex: 'operation',
              ellipsis: true
            },
        ],
        dataSource: [],
      });
      const clickQuery = () => {
        queryList()
      };
      const clickInit = () => {
        state.formState.corp_name = null
        state.formState.legal_name = null
        state.formState.industry = null
        state.formState.start_query_num = null
        state.formState.end_query_num = null
        state.formState.address = null
        state.pagination.current = 1
        queryList()
      };
      const queryList = () => {
        // 获取列表数据
        state.formState['current'] = state.pagination.current
        state.formState['pageSize'] = state.pagination.pageSize
        openIndex(state.formState).then((res) => {
          if (res) {
            if (res.total) {
              state.dataSource = res.data;
              state.pagination.total = res.total;
            } else {
              state.dataSource = res;
            }
          }
        });
      };
      queryList()
      
      return {
        clickInit,
        queryList,
        clickQuery,
        ...toRefs(state)
      };
    }
  };
  </script>
  <style scoped>
    .detailBtn{
      margin-right: 15px;
    }
  </style>