import { request } from "./request.js";
export function MenuLists(data = {}) {
    return request({
        url: "Bew/menuLists",
        method: "post",
        data
    });
}
export function MenuSave(data = {}) {
    return request({
        url: "Bew/menuSave",
        method: "post",
        data,
    });
}
export function MenuDel(data = {}) {
    return request({
        url: "Bew/menuDel",
        method: "post",
        data,
    });
}