<template>
    <div v-if="showModel">
      <a-modal v-model:visible="showModel" :title="titleVal" width="800px" :footer="null" @cancel="cancelModel">
        <a-form
          :model="obj"
          :rules="rules"
          :wrapper-col="{ span: 12}"
          @finish="clickOk"
        >
          <a-row :gutter="24">
            <a-col :span="12">
              <a-form-item
                label="公司地址"
                name="corp_province"
                :rules="[{ required: true, message: '请选择地区!' }]"
              >
                <a-cascader 
                  ref="cascader"
                  :field-names="{ label: 'name', value: 'code' }" 
                  v-model:value="areaValue" 
                  :options="areaList" 
                  placeholder="所在地区" 
                  @change="changeArea"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="详细地址"
                name="corp_address"
                :rules="[{ required: true, message: '请输入详细地址!' }]"
              >
                <a-input allowClear v-model:value="obj.corp_address" placeholder="请输入详细地址" maxlength="50"/>
              </a-form-item>
            </a-col>
          </a-row>
          <a-row :gutter="24">
            <a-col :span="12">
              <a-form-item
                label="营业执照"
                name="credit_code"
                :rules="[{ required: true, message: '请输入企业营业执照', trigger: 'blur' },
                { pattern: /^([0-9A-HJ-NPQRTUWXY]{2}\d{6}[0-9A-HJ-NPQRTUWXY]{10}|[1-9]\d{14})$/, message: '企业营业执照格式不正确' }]"
              >
                <a-input allowClear v-model:value="obj.credit_code" placeholder="请输入营业执照" maxlength="18"/>
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
                label="发证日期"
                name="regsit_time"
                :rules="[{ required: true, message: '请选择企业注册时间!' }]"
              >
                <a-date-picker
                  v-model:value="obj.regsit_time"
                  placeholder="请选择营业执照发证日期"
                  style="width: 100%"
                  format="YYYY-MM-DD"
                  valueFormat="YYYY-MM-DD HH-mm-ss"
                />
              </a-form-item>
            </a-col>
          </a-row>
          
          <a-row :gutter="24">
            <a-col :span="12">
              <a-form-item
                label="所属行业"
                name="industry_classify_label"
                :rules="[{ required: true, message: '请选择所属行业!' }]"
              >
                <a-select
                  v-model:value="obj.industry_classify_label"
                  allowClear
                >
                  <a-select-option v-for="(item, index) in INDUSTRY_CLASSIFY" :value="item.the_value" :key="index">{{ item.the_key }}</a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
              label="注册资金"
              name="regsit_capital"
              :rules="[{ required: true, message: '请输入注册资金', trigger: 'blur' },
              { min: 1, type: 'number', message: '金额需大于0', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 240px;" v-model:value="obj.regsit_capital" :min="0" :max="99999"  placeholder="请输入注册资金"/>万元
            </a-form-item>
            </a-col>
          </a-row>
          <a-row :gutter="24">
            <a-col :span="12">
              <a-form-item
                label="法人股东"
                name="legal"
              >
                <a-radio-group v-model:value="obj.legal">
                  <a-radio :value="1">法人</a-radio>
                  <a-radio :value="0">股东</a-radio>
                </a-radio-group>
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
              label="占股比例"
              name="stoct_ratio"
              :rules="[{ required: true, message: '请输入占股比例!' }]"
            >
              <a-input-number allowClear style="width: 255px;" v-model:value="obj.stoct_ratio" :min="0" :max="99999"  placeholder="请输入占股比例"/>%
            </a-form-item>
            </a-col>
          </a-row>
          <a-row :gutter="24">
            <a-col :span="12">
              <a-form-item
                label="纳税等级"
                name="pay_tax_level_label"
                :rules="[{ required: true, message: '请选择纳税等级!' }]"
              >
                <a-select
                  v-model:value="obj.pay_tax_level_label"
                  allowClear
                >
                  <a-select-option v-for="(item, index) in PAY_TAX_LEVEL" :value="item.the_value" :key="index">{{ item.the_key }}</a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item
              label="国税密码"
              name="state_tax_pwd"
            >
              <a-input v-model:value="obj.state_tax_pwd" placeholder="请输入国税密码" maxlength="20" />
            </a-form-item>
            </a-col>
          </a-row>
          <a-row :gutter="24">
            <a-col :span="12">
              <a-form-item
                label="企业邮箱"
                name="corp_mail"
                :rules="[{ pattern: /^[A-Za-z0-9]+([_\.][A-Za-z0-9]+)*@([A-Za-z0-9\-]+\.)+[A-Za-z]{2,6}$/, message: '企业邮箱格式不正确' }]"
              >
                <a-input v-model:value="obj.corp_mail" placeholder="请输入企业邮箱" maxlength="30" />
              </a-form-item>
            </a-col>
          </a-row>
          <a-row :gutter="24">
            <a-col :span="8">
              <a-form-item
                label="税务年度"
                name="tax_one_date"
                :rules="[{ required: true, message: '请选择税务时间!' }]"
              >
                <a-date-picker
                  v-model:value="obj.tax_one_date"
                  placeholder="选择税务年度"
                  picker="year"
                  format="YYYY"
                  valueFormat="YYYY"
                />
              </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item
              label="开票金额"
              name="tax_one_invoice"
              :rules="[{ required: true, message: '请输入开票金额', trigger: 'blur' },
              { min: -1, type: 'number', message: '金额需正整数', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 120px;" v-model:value="obj.tax_one_invoice" :min="0" :max="99999"  placeholder="请输入开票金额"/>万元
            </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item
              label="纳税金额"
              name="tax_one_payer"
              :rules="[{ required: true, message: '请输入纳税金额', trigger: 'blur' },
              { min: -1, type: 'number', message: '金额需正整数', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 120px;" v-model:value="obj.tax_one_payer" :min="0" :max="99999"  placeholder="请输入纳税金额"/>万元
            </a-form-item>
            </a-col>
          </a-row>
          <a-row :gutter="24">
            <a-col :span="8">
              <a-form-item
                label="税务年度"
                name="tax_two_date"
                :rules="[{ required: true, message: '请选择税务时间!' }]"
              >
                <a-date-picker
                  v-model:value="obj.tax_two_date"
                  placeholder="选择税务年度"
                  picker="year"
                  format="YYYY"
                  valueFormat="YYYY"
                />
              </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item
              label="开票金额"
              name="tax_two_invoice"
              :rules="[{ required: true, message: '请输入开票金额', trigger: 'blur' },
              { min: -1, type: 'number', message: '金额需正整数', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 120px;" v-model:value="obj.tax_two_invoice" :min="0" :max="99999"  placeholder="请输入开票金额"/>万元
            </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item
              label="纳税金额"
              name="tax_two_payer"
              :rules="[{ required: true, message: '请输入纳税金额', trigger: 'blur' },
              { min: -1, type: 'number', message: '金额需正整数', trigger: 'blur' }]"
            >
              <a-input-number allowClear style="width: 120px;" v-model:value="obj.tax_two_payer" :min="0" :max="99999"  placeholder="请输入纳税金额"/>万元
            </a-form-item>
            </a-col>
          </a-row>
          <a-row :gutter="24">
            <a-col :span="8">
              <a-form-item
                label="税务年度"
                name="tax_three_date"
              >
                <a-date-picker
                  v-model:value="obj.tax_three_date"
                  placeholder="选择税务年度"
                  picker="year"
                  format="YYYY"
                  valueFormat="YYYY"
                />
              </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item
              label="开票金额"
              name="tax_three_invoice"
            >
              <a-input-number allowClear style="width: 120px;" v-model:value="obj.tax_three_invoice" :min="0" :max="99999"  placeholder="请输入开票金额"/>万元
            </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item
              label="纳税金额"
              name="tax_three_payer"
            >
              <a-input-number allowClear style="width: 120px;" v-model:value="obj.tax_three_payer" :min="0" :max="99999"  placeholder="请输入纳税金额"/>万元
            </a-form-item>
            </a-col>
          </a-row>



          <a-row :gutter="24">
            <a-col :span="6" :offset="18">
              <a-button  @click="cancelModel" class="cancelModel">取消</a-button>
              <a-button type="primary" html-type="submit" >确定</a-button>
            </a-col>
          </a-row>
  
        </a-form>
      </a-modal>
    </div>
  </template>
  <script>
  import { reactive, watch, toRefs,defineComponent   } from "vue";
  import { message } from 'ant-design-vue';
  import { twoCustomerAddOrEdit } from "@/network/business.js";
  import { getCurrentInstance } from "@vue/runtime-core";  // 引入getCurrentInstance
  import { area } from '@/utils/area.js';
  import { useStore } from "vuex";
  export default defineComponent({
    name: "ShiftCustomer", // 转入客户
    components: {},
    props: {
      subType: {
        type: String,
        default: '1', // 提交类型  1：添加  2：编辑
      },
      titleVal: {
        type: String,
        default: '进二阶',
      },
      visible: {
        type: Boolean,
        required: false
      },
      customerObj: {
        type: Object,
        default: () => {},
        required: false
      }
    },
    setup(props, context) {
      let $bus = getCurrentInstance().appContext.config.globalProperties.$bus; // 声明$bus
      const { visible, customerObj, titleVal, subType } = toRefs(props);
      const store = useStore();

      const state = reactive({
        areaValue: null,
        areaList: area,
        showModel: false,
        obj: {
          id: null,
          legal: 1,
          corp_province: null,
          corp_city: null,
          corp_county: null,
          corp_address: null,
          credit_code: null,
          regsit_time: null,
          industry_classify_label: null,
          regsit_capital: null,
          stoct_ratio: null,
          pay_tax_level_label: null,
          state_tax_pwd: null,
          corp_mail: null,
          tax_one_date: null,
          tax_one_invoice: null,
          tax_one_payer: null,
          tax_two_date: null,
          tax_two_invoice: null,
          tax_two_payer: null,
          tax_three_date: null,
          tax_three_invoice: null,
          tax_three_payer: null,
        },
        INDUSTRY_CLASSIFY: [],
        PAY_TAX_LEVEL: []
      })
      watch(() => visible.value, (newVal) => {
        getConfig()
        state.obj.id = customerObj.value.id
        if (titleVal.value == '编辑二阶') {
          state.obj.legal = parseInt(customerObj.value.legal)
          state.obj.corp_province = customerObj.value.corp_province
          state.obj.corp_city = customerObj.value.corp_city
          state.obj.corp_county = customerObj.value.corp_county
          state.obj.corp_address = customerObj.value.corp_address
          state.obj.credit_code = customerObj.value.credit_code
          state.obj.regsit_time = customerObj.value.regsit_time 
          state.obj.industry_classify_label = customerObj.value.industry_classify_label
          state.obj.regsit_capital = customerObj.value.regsit_capital
          state.obj.stoct_ratio = customerObj.value.stoct_ratio
          state.obj.pay_tax_level_label = customerObj.value.pay_tax_level_label
          state.obj.state_tax_pwd = customerObj.value.state_tax_pwd
          state.obj.corp_mail = customerObj.value.corp_mail
          state.obj.tax_one_date = customerObj.value.tax_one_date
          state.obj.tax_one_invoice = customerObj.value.tax_one_invoice
          state.obj.tax_one_payer = customerObj.value.tax_one_payer
          state.obj.tax_two_date = customerObj.value.tax_two_date
          state.obj.tax_two_invoice = customerObj.value.tax_two_invoice
          state.obj.tax_two_payer = customerObj.value.tax_two_payer
          state.obj.tax_three_date = customerObj.value.tax_three_date
          state.obj.tax_three_invoice = customerObj.value.tax_three_invoice
          state.obj.tax_three_payer = customerObj.value.tax_three_payer
          state.areaValue = [customerObj.value.corp_province, customerObj.value.corp_city, customerObj.value.corp_county ]
        } 

        state.showModel = newVal
      },{deep: true})
      const getConfig = () => {
        let configData = store.state.configs
        if (configData && configData.length > 0) {
          state.INDUSTRY_CLASSIFY = configData.filter(ele => {
            return ele.column_name == 'industry_classify_label'
          })
          state.PAY_TAX_LEVEL = configData.filter(ele => {
            return ele.column_name == 'pay_tax_level'
          })
        }
      }
      const clickOk = () => {
        let data = {
          obj: state.obj,
          type: subType.value
        }
        twoCustomerAddOrEdit(data).then(res => {
          if (res) {
            if (titleVal.value == '编辑二阶') {
              message.success('编辑成功');
            } else {
              message.success('进二阶成功');
            }
            cancelModel()
            $bus.emit("refreshCustomerList")
          }
        })
      }
      const cancelModel = () => {
        state.areaValue = null
        state.obj = {
          id: null,
          legal: 1,
          corp_province: null,
          corp_city: null,
          corp_county: null,
          corp_address: null,
          credit_code: null,
          regsit_time: null,
          industry_classify_label: null,
          regsit_capital: null,
          stoct_ratio: null,
          pay_tax_level_label: null,
          state_tax_pwd: null,
          corp_mail: null,
          tax_one_date: null,
          tax_one_invoice: null,
          tax_one_payer: null,
          tax_two_date: null,
          tax_two_invoice: null,
          tax_two_payer: null,
          tax_three_date: null,
          tax_three_invoice: null,
          tax_three_payer: null,
        }
        context.emit('closeBox')
      }

      const changeArea = (e) => {
        if (e  && e.length > 0) {
          state.obj.corp_province = e[0]
          state.obj.corp_city = e[1]
          state.obj.corp_county = e[2]
        } else {
          state.obj.corp_province = null
          state.obj.corp_city = null
          state.obj.corp_county = null
        }
      }
      
      return {
        changeArea,
        clickOk,
        cancelModel,
        ...toRefs(state)
      };
    } 
  });
  </script>
  <style scoped>
    .cancelModel{
      margin-right: 20px;
    }
    /deep/ .ant-col-12{
      max-width: 100%;
    }
  </style>