<template>
  <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
  </a-table>
</template>
<script>
import { reactive, toRefs  } from "vue";
import { customerFollowDetail } from "@/network/follow.js";
export default {
  name: "FollowRecord", 
  components: {},
  props: {
    customerId: {
      type: Number,
      default: 0
    }
  },
  setup(props) {
    const { customerId } = toRefs(props);
    const state = reactive({
      pagination: {
          total: 0,
          current: 1,
          pageSize: 10,
          onChange: (e) => {
            state.pagination.current = e
            queryList()
          }
        },
        columns: [ {
          title: "跟进员工",
          dataIndex: "admin_user_name",
          width: '120px'
        }, {
          title: "所属部门",
          dataIndex: "dept_name",
          width: '120px'
        }, {
          title: "跟进方式",
          dataIndex: "follow_type_label",
          width: '160px'
        },{
          title: "跟进目的",
          dataIndex: "follow_goal_label",
          width: '160px'
        },{
          title: "跟进内容",
          dataIndex: "content"
        },{
          title: "跟进时间",
          dataIndex: "create_time",
          width: '180px'
        },],
        dataSource: [],
    })

    const queryList = () => {
      let data = {
        customerId: customerId.value,
        pageSize: state.pagination.pageSize,
        current:state.pagination.current
      }
      customerFollowDetail(data).then((res) => {
        if (res) {
          state.dataSource = res.data;
          state.pagination.total = res.total;
        }
      });
    }
    queryList()
    return {
      queryList,
      ...toRefs(state)
    };
  } 
};
</script>
<style scoped>
  /deep/ .ant-table-cell{
    padding: 12px 16px;
  }
</style>