<template>
    <div>
      <a-form
        :model="formState"
        layout="inline"
      >
        <a-form-item
          label="企业名称"
          name="corp_name"
        >
          <a-input allowClear v-model:value="formState.corp_name" placeholder="请输入企业名称"/>
        </a-form-item>
        <a-form-item
          label="客户姓名"
          name="name"
        >
          <a-input allowClear v-model:value="formState.name" placeholder="请输入客户姓名"/>
        </a-form-item>
  
        <a-form-item
          label="所属行业"
          name="industry_classify_label"
        >
          <a-input allowClear v-model:value="formState.industry_classify_label" placeholder="请输入所属行业"/>
        </a-form-item>
  
        <a-form-item
          label="纳税等级"
          name="pay_tax_level_label"
        >
          <a-input allowClear v-model:value="formState.pay_tax_level_label" placeholder="请输入所属行业"/>
        </a-form-item>
        <a-form-item
          label="资金需求"
          name="money_amount"
        >
          <a-input allowClear v-model:value="formState.money_amount" placeholder="请输入所属行业"/>万
        </a-form-item>
        <a-form-item
          label="地区"
          name="areaValue"
          class="areaBox"
        >
          <a-cascader 
            ref="cascader"
            :field-names="{ label: 'name', value: 'code' }" 
            v-model:value="areaValue" 
            :options="areaList" 
            placeholder="所在地区" 
            @change="changeArea"
          />
        </a-form-item>
  
        <a-form-item
          label="是否法人"
          name="legal"
        >
          <a-select
            v-model:value="formState.legal"
            style="width: 120px"
            allowClear
            placeholder="是否法人"
          >
            <a-select-option value="1">是</a-select-option>
            <a-select-option value="0">否</a-select-option>
          </a-select>
        </a-form-item>
        
        <a-form-item
          label="是否保护"
          name="protect"
        >
          <a-select
            v-model:value="formState.protect"
            style="width: 120px"
            allowClear
            placeholder="是否保护"
          >
            <a-select-option value="1">是</a-select-option>
            <a-select-option value="0">否</a-select-option>
          </a-select>
        </a-form-item>
  
        <a-form-item
          label="是否加微信"
          name="add_wechat"
        >
          <a-select
            v-model:value="formState.add_wechat"
            style="width: 120px"
            allowClear
            placeholder="是否加微信"
          >
            <a-select-option value="1">是</a-select-option>
            <a-select-option value="0">否</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label="状态"
          name="status"
        >
          <a-select
          v-model:value="formState.status"
          style="width: 120px"
          allowClear
          placeholder="状态"
        >
          <template v-for="(item, index) in customerStatus" :key="index">
            <a-select-option  :value="item.value">{{item.label}}</a-select-option>
          </template>
        </a-select>
        </a-form-item>
  
    
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button type="primary" html-type="submit" @click="clickQuery">查询</a-button>
        </a-form-item>
        <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
          <a-button @click="clickInit">重置</a-button>
        </a-form-item>
      </a-form>
      <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
        <template #bodyCell="{ column, record}">
          <template v-if="column.dataIndex === 'expire_time'">
            <span v-if="record.protect == 1" :style="{color: record.expire_status}">{{  record.expire_time ? record.expire_time.substr(5,record.expire_time.length) : '--' }}</span>
            <span v-if="record.protect == 0">未保护</span>
            <a-tag class="tagStyle" :color="record.protect == 1 ? 'green' : 'red'" >{{ record.protect == 1 ? '已' : '未' }}保护</a-tag>
          </template>
          <template v-if="column.dataIndex === 'corp_name'">
            <span>{{ record.corp_name }} / {{record.name}}</span>
          </template>
          <template v-if="column.dataIndex === 'add_wechat'">
            <a-tag  v-if="record.add_wechat == 0" color="red">未加</a-tag>
            <a-tag  v-else color="blue">已加</a-tag>
          </template>
          <template v-if="column.dataIndex === 'know_cost'">
            <a-tag  v-if="record.know_cost == 0" color="red">未知</a-tag>
            <a-tag  v-else color="blue">知晓</a-tag>
          </template>
          <template v-if="column.dataIndex === 'assets_house_type_label'">
            <span>{{ record.dassets_house_type_label ? '房:' + record.dassets_house_type_label : '房:未知' }} | {{ record.assets_car_type_label ? (record.dassets_house_type_label == '无' ? '车:' + record.dassets_house_type_label : '车:' + (record.assets_car_naked_price ? record.assets_car_naked_price : 0) + '万') : '未知' }}</span>
          </template>
  
          <template v-if="column.dataIndex === 'pay_tax_level_label'">
            <a-popover title="近3年开票纳税">
              <template #content>
                <div v-if="record.tax_one_date">
                  {{ record.tax_one_date }}年: 开票{{ record.tax_one_invoice }}万 丨 纳税{{ record.tax_one_payer }}万
                </div>
                <div v-if="record.tax_two_date">
                  {{ record.tax_two_date }}年: 开票{{ record.tax_two_invoice }}万 丨 纳税{{ record.tax_two_payer }}万
                </div>
                <div v-if="record.tax_three_date">
                  {{ record.tax_three_date }}年: 开票{{ record.tax_three_invoice }}万 丨 纳税{{ record.tax_three_payer }}万
                </div>
              </template>
              <a>{{ record.pay_tax_level_label }} ({{ record.tax_one_invoice }}万 | {{ record.tax_one_payer }}万)</a>
            </a-popover>
          </template>
          
          <template v-if="column.dataIndex === 'status'">
            <a-tag  v-if="record.status == 'ONE_STAGE'" color="red">一阶客户</a-tag>
            <a-tag  v-if="record.status == 'TWO_STAGE'" color="pink">二阶客户</a-tag>
            <a-tag  v-if="record.status == 'THREE_STAGE'" color="orange">三阶客户</a-tag>
            <a-tag  v-if="record.status == 'INQUIRY'" color="blue">预审客户</a-tag>
            <a-tag  v-if="record.status == 'LOAN'" color="green">放款客户</a-tag>
          </template>
  
          <template v-if="column.dataIndex === 'operation'">
            <a class="detailBtn" @click="clickDetail(record)">详情</a>
          </template>
        </template>
      </a-table>

      <customer-detail :visible="visibleDetail" :customerId="customerId" @closeBox="closeDetailComponent"></customer-detail>
    </div>
  </template>
  <script>
  import { reactive, toRefs  } from "vue";
  import { deptCustomerLists} from "@/network/business.js";
  import { getCurrentInstance } from "@vue/runtime-core";  // 引入getCurrentInstance
  import { useStore } from "vuex";
  import { area } from '@/utils/area.js';
  import CustomerDetail from '@/components/CustomerDetail.vue'
  export default {
    components: {
      CustomerDetail
    },
    setup() {
      let $bus = getCurrentInstance().appContext.config.globalProperties.$bus; // 声明$bus
      $bus.on("refreshCustomerList", () => {
        state.pagination.current = 1
        queryList()
      });
      const store = useStore();
      const state = reactive({
        visibleDetail: false,
        customerId: null,
        areaValue: null,
        areaList: area,
        customerStatus: store.state.customerStatus,
        formState: {
          corp_name: null,
          status: null,
          name: null,
          add_wechat: null,
          money_amount: null,
          corp_province: null,
          corp_city: null,
          corp_county: null,
          industry_classify_label: null,
          legal: null,
          pay_tax_level_label: null,
          protect: null,
        },
        pagination: {
          total: 0,
          current: 1,
          pageSize: 10,
          onChange: (e) => {
            state.pagination.current = e
            queryList()
          }
        },
        columns: [
            {
                title: "到期时间",
                dataIndex: "expire_time",
                width: '200px',
                ellipsis: true
            },
            {
                title: "公司/法人",
                dataIndex: "corp_name",
                ellipsis: true
            },
            {
                title: "纳税开票",
                dataIndex: "pay_tax_level_label",
                width: '190px',
                ellipsis: true
            },
            {
                title: "微信",
                dataIndex: "add_wechat",
                width: '90px',
                ellipsis: true
            },
            {
                title: "费用",
                dataIndex: "know_cost",
                width: '90px',
                ellipsis: true
            },
            {
                title: "状态",
                dataIndex: "status",
                width: '120px',
                ellipsis: true
            },
            {
                title: "房产车辆",
                width: '190px',
                dataIndex: "assets_house_type_label",
                ellipsis: true
            },
            {
                title: "录入时间",
                dataIndex: "create_time",
                width: '180px',
                ellipsis: true
            },
            {
              title: '操作',
              width: '260px',
              dataIndex: 'operation',
              ellipsis: true
            },
        ],
        dataSource: [],
      });
      const clickQuery = () => {
        queryList()
      };
      const clickInit = () => {
        state.areaValue = null
        state.formState.corp_name = null
        state.formState.status = null
        state.formState.name = null
        state.formState.add_wechat = null
        state.formState.money_amount = null
        state.formState.corp_province = null
        state.formState.corp_city = null
        state.formState.corp_county = null
        state.formState.industry_classify_label = null
        state.formState.legal = null
        state.formState.pay_tax_level_label = null
        state.formState.protect = null
        state.pagination.current = 1
        queryList()
      };
      const queryList = () => {
        // 获取列表数据
        state.formState['current'] = state.pagination.current
        state.formState['pageSize'] = state.pagination.pageSize
        deptCustomerLists(state.formState).then((res) => {
          if (res) {
            state.dataSource = res.data;
            state.pagination.total = res.total;
          }
        });
      };
      queryList()
    
  
  
      const changeArea = (e) => {
        if (e  && e.length > 0) {
          state.formState.corp_province = e[0]
          state.formState.corp_city = e[1]
          state.formState.corp_county = e[2]
        } else {
          state.formState.corp_province = null
          state.formState.corp_city = null
          state.formState.corp_county = null
        }
      }
  
      const clickDetail = (e) => {
        state.customerId = e.id
        state.visibleDetail = true
      }
  
      const closeDetailComponent = () => {
        state.visibleDetail = false
      }
      return {
        closeDetailComponent,
        clickDetail,
        changeArea,
        clickInit,
        queryList,
        clickQuery,
        ...toRefs(state)
      };
    }
  };
  </script>
  <style scoped>
    .detailBtn{
      margin-right: 15px;
    }
   
    /deep/ .ant-form-item{
      margin-bottom: 15px !important;
    }
    /deep/ .ant-form-item-control-input-content{
      display: flex;
      line-height: 32px;
    }
 
    .areaBox{
      width: 245px;
    }
  </style>