<template>
  <div>
    <a-form
      :model="formState"
      layout="inline"
    >
      <a-form-item
        label="企业名称"
        name="corp_name"
      >
        <a-input allowClear v-model:value="formState.corp_name" placeholder="请输入企业名称"/>
      </a-form-item>
      <a-form-item
        label="客户姓名"
        name="name"
      >
        <a-input allowClear v-model:value="formState.name" placeholder="请输入客户姓名"/>
      </a-form-item>

      <a-form-item
        label="所属行业"
        name="industry_classify_label"
      >
        <a-input allowClear v-model:value="formState.industry_classify_label" placeholder="请输入所属行业"/>
      </a-form-item>

      <a-form-item
        label="纳税等级"
        name="pay_tax_level_label"
      >
        <a-input allowClear v-model:value="formState.pay_tax_level_label" placeholder="请输入所属行业"/>
      </a-form-item>
      <a-form-item
        label="资金需求"
        name="money_amount"
      >
        <a-input allowClear v-model:value="formState.money_amount" placeholder="请输入所属行业"/>万
      </a-form-item>
      <a-form-item
        label="地区"
        name="areaValue"
        class="areaBox"
      >
        <a-cascader 
          ref="cascader"
          :field-names="{ label: 'name', value: 'code' }" 
          v-model:value="areaValue" 
          :options="areaList" 
          placeholder="所在地区" 
          @change="changeArea"
        />
      </a-form-item>

      <a-form-item
        label="是否法人"
        name="legal"
      >
        <a-select
          v-model:value="formState.legal"
          style="width: 120px"
          allowClear
          placeholder="是否法人"
        >
          <a-select-option value="1">是</a-select-option>
          <a-select-option value="0">否</a-select-option>
        </a-select>
      </a-form-item>
      
      <a-form-item
        label="是否保护"
        name="protect"
      >
        <a-select
          v-model:value="formState.protect"
          style="width: 120px"
          allowClear
          placeholder="是否保护"
        >
          <a-select-option value="1">是</a-select-option>
          <a-select-option value="0">否</a-select-option>
        </a-select>
      </a-form-item>

      <a-form-item
        label="是否加微信"
        name="add_wechat"
      >
        <a-select
          v-model:value="formState.add_wechat"
          style="width: 120px"
          allowClear
          placeholder="是否加微信"
        >
          <a-select-option value="1">是</a-select-option>
          <a-select-option value="0">否</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item
        label="状态"
        name="status"
      >
        <a-select
        v-model:value="formState.status"
        style="width: 120px"
        allowClear
        placeholder="状态"
      >
        <template v-for="(item, index) in customerStatus" :key="index">
          <a-select-option  :value="item.value">{{item.label}}</a-select-option>
        </template>
      </a-select>
      </a-form-item>

  
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
        <a-button type="primary" html-type="submit" @click="clickQuery">查询</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ offset: 8, span: 16 }">
        <a-button @click="clickInit">重置</a-button>
      </a-form-item>
    </a-form>
    <a-button type="primary" class="addCustomer" @click="addCustomer">新增</a-button>
    <div class="tipBox">保护客户数 <span class="tipsNum">{{num}}/{{customer_num}}</span></div>
    <a-table :dataSource="dataSource" :columns="columns" bordered :pagination="pagination">
      <template #bodyCell="{ column, record}">
        <template v-if="column.dataIndex === 'expire_time'">
          <span v-if="record.protect == 1" :style="{color: record.expire_status}">{{  record.expire_time ? record.expire_time.substr(5,record.expire_time.length) : '--' }}</span>
          <a-tag @click="clickProtect(record)" class="tagStyle" :color="record.protect == 1 ? 'green' : 'red'" >{{ record.protect == 1 ? '已' : '未' }}保护</a-tag>
        </template>
        <template v-if="column.dataIndex === 'corp_name'">
          <span>{{ record.corp_name }} / {{record.name}}</span>
        </template>
        <template v-if="column.dataIndex === 'add_wechat'">
          <a-tag  v-if="record.add_wechat == 0" color="red">未加</a-tag>
          <a-tag  v-else color="blue">已加</a-tag>
        </template>
        <template v-if="column.dataIndex === 'know_cost'">
          <a-tag  v-if="record.know_cost == 0" color="red">未知</a-tag>
          <a-tag  v-else color="blue">知晓</a-tag>
        </template>
        <template v-if="column.dataIndex === 'assets_house_type_label'">
          <span>{{ record.dassets_house_type_label ? '房:' + record.dassets_house_type_label : '房:未知' }} | {{ record.assets_car_type_label ? (record.dassets_house_type_label == '无' ? '车:' + record.dassets_house_type_label : '车:' + (record.assets_car_naked_price ? record.assets_car_naked_price : 0) + '万') : '未知' }}</span>
        </template>

        <template v-if="column.dataIndex === 'pay_tax_level_label'">
          <a-popover title="近3年开票纳税">
            <template #content>
              <div v-if="record.tax_one_date">
                {{ record.tax_one_date }}年: 开票{{ record.tax_one_invoice }}万 丨 纳税{{ record.tax_one_payer }}万
              </div>
              <div v-if="record.tax_two_date">
                {{ record.tax_two_date }}年: 开票{{ record.tax_two_invoice }}万 丨 纳税{{ record.tax_two_payer }}万
              </div>
              <div v-if="record.tax_three_date">
                {{ record.tax_three_date }}年: 开票{{ record.tax_three_invoice }}万 丨 纳税{{ record.tax_three_payer }}万
              </div>
            </template>
            <a>{{ record.pay_tax_level_label }} ({{ record.tax_one_invoice }}万 | {{ record.tax_one_payer }}万)</a>
          </a-popover>
        </template>
        
        <template v-if="column.dataIndex === 'status'">
          <a-tag  v-if="record.status == 'ONE_STAGE'" color="red">一阶客户</a-tag>
          <a-tag  v-if="record.status == 'TWO_STAGE'" color="pink">二阶客户</a-tag>
          <a-tag  v-if="record.status == 'THREE_STAGE'" color="orange">三阶客户</a-tag>
          <a-tag  v-if="record.status == 'INQUIRY'" color="blue">预审客户</a-tag>
          <a-tag  v-if="record.status == 'LOAN'" color="green">放款客户</a-tag>
        </template>

        <template v-if="column.dataIndex === 'operation'">
          <a class="detailBtn" @click="editCustomer(record)">编辑</a>

          <a class="detailBtn" v-if="record.status == 'ONE_STAGE'" @click="goTwoState(record)">进二阶</a>
          <a class="detailBtn" v-if="record.status == 'TWO_STAGE'" @click="goThreeState(record)">进三阶</a>
          <!-- <a class="detailBtn" v-if="record.status == 'THREE_STAGE'">提交预审</a> -->
          <a class="detailBtn" @click="followCustomer(record)">跟进</a>
          <a class="detailBtn" @click="clickDetail(record)">详情</a>
          <a class="deleteBtn" @click="Del(record)" v-if="record.protect == 0">删除</a>
        </template>
      </template>
    </a-table>
    
    <customer-one :visible="visible" :customerObj="customerObj" :titleVal="titleVal"  @closeBox="closeComponent"></customer-one>
    <customer-two :visible="visibleTwo" :customerObj="customerObj" :titleVal="titleVal" :subType="subType"  @closeBox="closeComponent"></customer-two>
    <customer-three :visible="visibleThree" :customerObj="customerObj" :titleVal="titleVal" :subType="subType"  @closeBox="closeComponent"></customer-three>
    <customer-follow :visible="visibleFollow" :customerObj="customerObj" @closeBox="closeComponent"></customer-follow>
    <customer-detail :visible="visibleDetail" :customerId="customerObj.id" @closeBox="closeDetailComponent"></customer-detail>
  </div>
</template>
<script>
import { reactive, toRefs  } from "vue";
import { customerLists, protectOrRelieveCustomer, customerDel} from "@/network/business.js";
import CustomerOne from '@/components/CustomerOne.vue'
import CustomerTwo from '@/components/CustomerTwo.vue'
import CustomerThree from '@/components/CustomerThree.vue'
import CustomerFollow from '@/components/CustomerFollow.vue'
import CustomerDetail from '@/components/CustomerDetail.vue'
import { Modal, message } from 'ant-design-vue';
import { getCurrentInstance } from "@vue/runtime-core";  // 引入getCurrentInstance
import { useStore } from "vuex";
import { area } from '@/utils/area.js';
export default {
  components: {
    CustomerThree,
    CustomerTwo,
    CustomerOne,
    CustomerFollow,
    CustomerDetail
  },
  setup() {
    let $bus = getCurrentInstance().appContext.config.globalProperties.$bus; // 声明$bus
    $bus.on("refreshCustomerList", () => {
      state.pagination.current = 1
      queryList()
    });
    const store = useStore();
    const state = reactive({
      subType: '1',
      titleVal:'新增客户',
      areaValue: null,
      areaList: area,
      customerStatus: store.state.customerStatus,
      customerObj: {
        corp_name: '',
        legal_name: '',
        user_id: JSON.parse(localStorage.getItem('user')).uid
      },
      visibleDetail: false,
      visibleTwo: false,
      visibleThree: false,
      visible: false,
      visibleFollow: false,
      num: 0,
      customer_num: 0,
      formState: {
        corp_name: null,
        status: null,
        name: null,
        add_wechat: null,
        money_amount: null,
        corp_province: null,
        corp_city: null,
        corp_county: null,
        industry_classify_label: null,
        legal: null,
        pay_tax_level_label: null,
        protect: null,
      },
      pagination: {
        total: 0,
        current: 1,
        pageSize: 10,
        onChange: (e) => {
          state.pagination.current = e
          queryList()
        }
      },
      columns: [
          {
              title: "到期时间",
              dataIndex: "expire_time",
              width: '200px',
              ellipsis: true
          },
          {
              title: "公司/法人",
              dataIndex: "corp_name",
              ellipsis: true
          },
          {
              title: "纳税开票",
              dataIndex: "pay_tax_level_label",
              width: '190px',
              ellipsis: true
          },
          {
              title: "微信",
              dataIndex: "add_wechat",
              width: '90px',
              ellipsis: true
          },
          {
              title: "费用",
              dataIndex: "know_cost",
              width: '90px',
              ellipsis: true
          },
          {
              title: "状态",
              dataIndex: "status",
              width: '120px',
              ellipsis: true
          },
          {
              title: "房产车辆",
              width: '190px',
              dataIndex: "assets_house_type_label",
              ellipsis: true
          },
          {
              title: "录入时间",
              dataIndex: "create_time",
              width: '180px',
              ellipsis: true
          },
          {
            title: '操作',
            width: '260px',
            dataIndex: 'operation',
            ellipsis: true
          },
      ],
      dataSource: [],
    });
    const clickQuery = () => {
      queryList()
    };
    const clickInit = () => {
      state.areaValue = null
      state.formState.corp_name = null
      state.formState.status = null
      state.formState.name = null
      state.formState.add_wechat = null
      state.formState.money_amount = null
      state.formState.corp_province = null
      state.formState.corp_city = null
      state.formState.corp_county = null
      state.formState.industry_classify_label = null
      state.formState.legal = null
      state.formState.pay_tax_level_label = null
      state.formState.protect = null
      state.pagination.current = 1
      queryList()
    };
    const queryList = () => {
      // 获取列表数据
      state.formState['current'] = state.pagination.current
      state.formState['pageSize'] = state.pagination.pageSize
      customerLists(state.formState).then((res) => {
        if (res) {
          state.dataSource = res.list.data;
          state.num = res.num
          state.customer_num = res.customer_num
          state.pagination.total = res.list.total;
        }
      });
    };
    queryList()
    const clickProtect = (row) => {
      let  titleText = '保护客户'
      let  contentText = '您确定保护' + row.corp_name + '该客户吗？'
      if (row.protect == 1) {
        titleText = '解除保护'
        contentText = '您确定解除' + row.corp_name + '的保护？剩余保护期将清空。'
      }
      
      Modal.confirm({
          title: titleText,
          content: contentText,
          cancelText: '取消',
          okText: '确定',
          onOk() {
            let data = {
              id: row.id,
              protect: row.protect,
              corp_name: row.corp_name
            }
            protectOrRelieveCustomer(data).then((res) => {
              if (res) {
                if (row.protect == 0) {
                  message.success('保护成功');
                } else {
                  message.success('解除成功');
                }
                clickInit()
              }
            });
          }
        });
    }
    const addCustomer = () => {
      state.titleVal = '新增客户'
      state.visible = true
    }
    
    const closeComponent = () => {
      state.customerObj = {}
      state.visible = false
      state.visibleTwo = false
      state.visibleThree = false
      state.visibleFollow = false
    }
    const closeDetailComponent = () => {
      state.visibleDetail = false
    }
 

    const followCustomer = (row) => {
      state.customerObj = row
      state.visibleFollow = true
    }
    const changeArea = (e) => {
      if (e  && e.length > 0) {
        state.formState.corp_province = e[0]
        state.formState.corp_city = e[1]
        state.formState.corp_county = e[2]
      } else {
        state.formState.corp_province = null
        state.formState.corp_city = null
        state.formState.corp_county = null
      }
    }
    const Del = (e) => {
      Modal.confirm({
        title: "警告",
        content: "删除后不能恢复，确定删除吗？",
        okText: "确认",
        cancelText: "取消",
        onOk() {
          let data = {
            id:e.id
          }
          customerDel(data).then((res) => {
            if (res) {
              message.success("删除成功", 1);
              queryList()
            }
          });
        }
      });
    };
    const clickDetail = (e) => {
      state.customerObj = e
      state.visibleDetail = true
    }

    const goTwoState = (e) => {
      state.customerObj = e
      state.titleVal = '进二阶'
      state.subType = '1'
      state.visibleTwo = true
    }
    const goThreeState = (e) => {
      state.customerObj = e
      state.titleVal = '进三阶'
      state.subType = '1'
      state.visibleThree = true
    }
    const editCustomer = (e) => {
      state.customerObj = e
      showPopup(e.status)
    }

    const showPopup = (status) => {
      if (status == 'ONE_STAGE') {
        state.visible = true
        state.titleVal = '编辑一阶'
      } else if (status == 'TWO_STAGE') {
        state.titleVal = '编辑二阶'
        state.subType = '2'
        state.visibleTwo = true
      } else if (status == 'THREE_STAGE') {
        state.titleVal = '编辑三阶'
        state.subType = '2'
        state.visibleThree = true
      }
    }

    return {
      closeDetailComponent,
      goTwoState,
      goThreeState,
      editCustomer,
      clickDetail,
      Del,
      followCustomer,
      changeArea,
      closeComponent,
      addCustomer,
      clickProtect,
      clickInit,
      queryList,
      clickQuery,
      ...toRefs(state)
    };
  }
};
</script>
<style scoped>
  .detailBtn{
    margin-right: 15px;
  }
  .tagStyle{
    margin-left: 10px;
    cursor: pointer;
  }
  .tipBox{
    margin-bottom: 20px;
  }
  .tipsNum{
    color: red;
  }
  /deep/ .ant-form-item{
    margin-bottom: 15px !important;
  }
  /deep/ .ant-form-item-control-input-content{
    display: flex;
    line-height: 32px;
  }
  .addCustomer{
    margin-bottom: 10px;
  }
  .areaBox{
    width: 245px;
  }
</style>